// I'm not sure if this component has any chrome or is just responsible for
// positioning itself 100%x100% on the screen, and maybe setting bg color

import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

const Overlay = styled.div`
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: linear-gradient(
    -1deg,
    rgba(248, 248, 248, 0.96) 0%,
    rgba(247, 252, 255, 0.96) 100%
  );
  z-index: 2147483647;
`;

export default function OverlayLayout({ children }) {
  return <Overlay>{children}</Overlay>;
}

OverlayLayout.propTypes = {
  children: PropTypes.node.isRequired,
};



