import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import Icon from './../basics/Icon';
import EmptyMessage from './../basics/EmptyMessage';
import { color, typography } from './../shared/styles';
import { inlineGlow, easing } from './../shared/animation';

// prettier-ignore
const Heading = styled.th`
  font-weight: ${typography.weight.extrabold};

  svg {
    transition: all 350ms ${easing.rubber};
    width: 12px;
    height: 12px;
    margin-left: .25em;
    display: inline-block;
    vertical-align: top;
    position: relative;
    top: 3px;

    opacity: ${props => props.sort === "none" ? '0' : '1'};
    transform: ${props => props.sort !== "descending" ? 'rotate(0deg)' : 'rotate(180deg)'};

    path { fill: ${color.mediumdark}; }
  }

  ${props => props.sortable && css`
    transition: all 200ms ease-in ;
    color: ${color.dark};

    &:hover {
      color: ${color.darkest};
      cursor: pointer;
    }
  `};
`;

// prettier-ignore
const TableView = styled.table`
  /* Resets */
  border-collapse: collapse;
  border-spacing: 0;

  td,
  th { padding: 0; }
  /* End Resets */

  font-size: ${typography.size.s2}px;
  line-height: 20px;

  float: left;
  margin: 1.5em 0;
  text-align: left;
  table-layout: fixed;
	width: 100%;

  th:first-of-type,
  td:first-of-type { padding-left: 30px; }

  th {
    color: ${color.darker};

    > span {
      overflow: hidden;
    	text-overflow: ellipsis;
    	white-space: nowrap;

      display: inline-block;
      margin: 0 0 .75rem;
      vertical-align: bottom;
    }

    &:not(:first-of-type) > span {
      padding-left: .86em;
      padding-right: .86em;
    }
  }

  tbody tr {
    background: ${color.lightest};

		&:nth-child(odd) { background: ${color.lighter}; }

    td {
      font-size: ${typography.size.s2}px;
      color: ${color.darker};
      line-height: 20px;
      padding-top: 10px;
      padding-bottom: 10px;
      position: relative;

      &:not(:first-of-type) {
        padding-left: .75em;
        padding-right: .75em;
      }

      &:last-of-type { padding-right: 30px; }

      &.truncate {
        overflow: hidden;
      	text-overflow: ellipsis;
      	white-space: nowrap;
      }

      .title {
        overflow: hidden;
      	text-overflow: ellipsis;
      	white-space: nowrap;

        font-weight: ${typography.weight.bold};
        display: inline-block;
        max-width: 100%;
        padding-right: .25em;
        position: relative;
        vertical-align: top;
      }

			.offsite {
        height: 16px;
        width: 16px;
        line-height: 1;
				display: inline-block;
				margin-left: .25em;
        vertical-align: text-bottom;

        &:hover { color: ${color.dark}; }
      }
    }
  }

  ${props => props.expandable && css`
    tbody tr {
      &:hover:nth-child(odd) td {
        background-color: #E3F3FF;
        box-shadow: ${color.mediumlight} 0 -1px 0 0 inset;
        cursor: row-resize;
      }

      /* reset */
      &:nth-child(odd) { background: ${color.lightest}; }
      &:not(:nth-child(odd)):last-child { border-bottom: none; }

      /* new bgs */
      &:nth-child(4n+1) { background: ${color.lighter}; }
      &:not(:nth-child(4n+1)):last-child { border-bottom: 1px solid ${color.lighter}; }
    }
  `}

  ${props => props.clickable && css`
    tbody tr {
      &:hover td {
        cursor: pointer;
        background-color: #E3F3FF;
      }
    }
  `}

  ${props => props.loading && css`
      ${Heading} span {
        ${inlineGlow};
      }
  `}
`;

export default class Table extends Component {
  constructor() {
    super();
    this.state = {
      openDrawers: {},
    };
    this.onToggleDrawer = this.onToggleDrawer.bind(this);
  }

  getChildContext() {
    return {
      openDrawers: this.state.openDrawers,
      onToggleDrawer: this.onToggleDrawer,
    };
  }

  onToggleDrawer(index) {
    const wasOpen = this.state.openDrawers[index];
    this.setState({
      openDrawers: {
        ...this.state.openDrawers,
        [index]: !wasOpen,
      },
    });
  }

  render() {
    const { loading, headings, sortable, expandable, children, ...props } = this.props;
    const loadingRow = (
      <tr>
        <td colSpan={headings.length}>{'\u00A0'}</td>
      </tr>
    );
    let childrenContent = (
      <tbody>
        {loadingRow}
        {loadingRow}
        {loadingRow}
        {loadingRow}
      </tbody>
    );
    if (!loading) {
      if (children) {
        childrenContent = <tbody>{children}</tbody>;
      } else {
        childrenContent = (
          <tbody>
            <tr>
              <td colSpan={headings.length}>
                <EmptyMessage />
              </td>
            </tr>
          </tbody>
        );
      }
    }

    return (
      <TableView sortable={sortable} expandable={expandable} loading={loading} {...props}>
        <thead>
          <tr>
            {headings.map(({ heading, sort }) => (
              <Heading key={heading} field="name" sort={sort} sortable={sortable}>
                <span>
                  {heading}
                  {sortable && <Icon icon="arrowdown" />}
                </span>
              </Heading>
            ))}
          </tr>
        </thead>
        {childrenContent}
      </TableView>
    );
  }
}

Table.propTypes = {
  loading: PropTypes.bool,
  headings: PropTypes.arrayOf(
    PropTypes.shape({
      heading: PropTypes.string.isRequired,
      sort: PropTypes.oneOf(['ascending', 'descending', 'none']),
    })
  ).isRequired,
  sortable: PropTypes.bool,
  expandable: PropTypes.bool,
  children: PropTypes.node,
};

Table.defaultProps = {
  loading: false,
  headings: [{ heading: 'Loading1' }, { heading: 'Loading2' }, { heading: 'Loading3' }],
  sortable: false,
  expandable: false,
  children: null,
};

Table.childContextTypes = {
  openDrawers: PropTypes.object,
  onToggleDrawer: PropTypes.func,
};



