import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { color, typography } from './../shared/styles';
import Link from './../basics/Link';

// prettier-ignore
const Title = styled.span``;

// prettier-ignore
const Item = styled(Link)`
  font-weight: ${typography.weight.bold};
  transition: all 150ms ease-out;
  line-height: 18px;
  padding: 7px 15px;
  display: block;
  transform: none !important;

  &:hover {
    background: #E3F3FF;
  }

  ${props => props.active && css`
    ${Title} { font-weight: ${typography.weight.extrabold}; }
    ${Title} { color: ${color.primary}; }
  `}
`;

// prettier-ignore
const List = styled.div`
  min-width: 180px;

  ${Item} + ${Item} {
    border-top: 1px solid ${color.mediumlight};
  }
`;

function TooltipLinkList({ links }) {
  return (
    <List>
      {links.map(({ title, href, onClick, active }) => (
        <Item key={title} href={href} onClick={onClick} active={active} tertiary>
          <Title>{title}</Title>
        </Item>
      ))}
    </List>
  );
}

TooltipLinkList.propTypes = {
  links: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string.isRequired,
      href: PropTypes.string,
      onClick: PropTypes.func,
      active: PropTypes.bool,
    }).isRequired
  ).isRequired,
};

export default TooltipLinkList;



