import React from 'react';
import PropTypes from 'prop-types';
import { withHandlers } from 'recompose';
import styled from 'styled-components';
import EmptyMessage from './../basics/EmptyMessage';
import SpecItem from './SpecItem';

const List = styled.div``;

const Item = withHandlers({
  onSelect: ({ spec: { id }, onSelect }) => () => onSelect(id),
})(({ spec, index, selected, onSelect, onSave, onDelete }) => (
  <SpecItem
    spec={spec}
    index={index}
    selected={selected}
    onSelect={onSelect}
    onSave={onSave && (values => onSave(spec.id, values))}
    onDelete={onDelete && (() => onDelete(spec.id))}
  />
));

export default function SpecList({
  loading,
  specs,
  selectedId,
  onSelect,
  onSave,
  onDelete,
  ...props
}) {
  if (loading) {
    return (
      <List {...props}>
        <SpecItem loading index={0} onSelect={() => 0} />
        <SpecItem loading index={1} onSelect={() => 0} />
        <SpecItem loading index={2} onSelect={() => 0} />
        <SpecItem loading index={3} onSelect={() => 0} />
        <SpecItem loading index={4} onSelect={() => 0} />
      </List>
    );
  }

  const SpecItems = specs.map((spec, index) => (
    <Item
      key={spec.id}
      spec={spec}
      index={index}
      selected={spec.id === selectedId}
      onSelect={onSelect}
      onSave={onSave}
      onDelete={onDelete}
    />
  ));

  const empty = (
    <EmptyMessage noun="specs">Add specs by saving the states of this component.</EmptyMessage>
  );

  return <List {...props}>{specs.length > 0 ? SpecItems : empty}</List>;
}

SpecList.propTypes = {
  loading: PropTypes.bool,
  specs: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,
      name: PropTypes.string,
      number: PropTypes.number,
      status: PropTypes.oneOf(['unsaved', 'saving', 'saved', 'deleted']).isRequired,
    })
  ),
  selectedId: PropTypes.string,
  onSelect: PropTypes.func.isRequired,
  onSave: PropTypes.func,
  onDelete: PropTypes.func,
};

SpecList.defaultProps = {
  loading: false,
  specs: [],
  selectedId: null,
  onSelect: () => 0,
  onSave: null,
  onDelete: null,
};



