/* eslint-env browser */
/* eslint-disable no-underscore-dangle */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { injectGlobal } from 'styled-components';
import { Popper, Manager, Target } from 'react-popper';

import { styles } from '../../../../../lib/components';
import ScannerButtons from './ScannerButtons';

const { color } = styles;

const CHROMATIC_SPEC_CLASSNAME = '__chromatic_spec__';
const UNSAVED_CLASSNAME = '__chromatic_unsaved__';
const CURRENTSAVED_CLASSNAME = '__chromatic_saved__';
const UNSAVEABLE_CLASSNAME = '__chromatic_unsaveable__';
const HOVER_CLASSNAME = '__chromatic_hover__';

// eslint-disable-next-line no-unused-expressions
injectGlobal`
  .${CHROMATIC_SPEC_CLASSNAME} {
    outline-width: 1px;
    outline-color: ${color.secondary};
    outline-style: dashed;
    transition: all 200ms ease-out;

    &.${HOVER_CLASSNAME} {
      cursor: pointer;
    }

    &.${CURRENTSAVED_CLASSNAME} {
      outline-color: ${color.primary};
    }

    &.${UNSAVEABLE_CLASSNAME} {
      outline-color: ${color.medium};
    }
  }
`;

function highlightSpecs(specs) {
  specs.forEach(spec => {
    const { domElement } = spec;
    domElement.classList.add(CHROMATIC_SPEC_CLASSNAME);
    if (spec.status === 'unsaveable') {
      domElement.classList.add(UNSAVEABLE_CLASSNAME);
    } else if (spec.component.specCount === 0) {
      domElement.classList.add(UNSAVED_CLASSNAME);
    } else if (spec.status === 'saved') {
      domElement.classList.add(CURRENTSAVED_CLASSNAME);
    }
    domElement.__chromatic_spec__ = spec;
  });
}

function unhighlightSpecs(specs) {
  specs.forEach(spec => {
    spec.domElement.classList.remove(CHROMATIC_SPEC_CLASSNAME);
    spec.domElement.classList.remove(UNSAVED_CLASSNAME);
    spec.domElement.classList.remove(HOVER_CLASSNAME);
  });
}

function hoverElement(element) {
  // Would it be faster to iterate our spec list? This works though
  document
    .querySelectorAll(`.${HOVER_CLASSNAME}`)
    .forEach(e => e.classList.remove(HOVER_CLASSNAME));

  if (element) {
    element.classList.add(HOVER_CLASSNAME);
  }
}

function firstAncestorWithSpec(element) {
  if (!element || !element.classList) {
    return null;
  }

  if (element.classList.contains(CHROMATIC_SPEC_CLASSNAME)) {
    return element;
  }

  return firstAncestorWithSpec(element.parentNode);
}

export default class WidgetScanner extends Component {
  constructor() {
    super();

    this.state = { activeSpec: null };
    this.onShow = this.onShow.bind(this);
    this.onHide = this.onHide.bind(this);
    this.onElementMouseOver = this.onElementMouseOver.bind(this);
    this.onElementMouseOut = this.onElementMouseOut.bind(this);
    this.onElementClick = this.onElementClick.bind(this);
  }

  componentWillMount() {
    highlightSpecs(this.props.specs);
  }

  componentDidMount() {
    // attach "live" handlers to the body
    document.body.addEventListener('mouseover', this.onElementMouseOver, false);
    document.body.addEventListener('mouseout', this.onElementMouseOut, false);
    document.body.addEventListener('click', this.onElementClick, false);
  }

  componentWillReceiveProps(props) {
    unhighlightSpecs(this.props.specs);
    highlightSpecs(props.specs);
  }

  componentWillUnmount() {
    document.body.removeEventListener('mouseover', this.onElementMouseOver);
    document.body.removeEventListener('mouseout', this.onElementMouseOut);
    document.body.removeEventListener('click', this.onElementClick);

    clearTimeout(this.timeout);
    unhighlightSpecs(this.props.specs);
  }

  onElementClick({ target }) {
    const element = firstAncestorWithSpec(target);
    if (element) {
      const { component: { name }, internalId, status } = element.__chromatic_spec__;
      if (status !== 'unsaveable') {
        this.props.onShowComponentSpecs(name, internalId);
      }
    }
  }

  onElementMouseOver({ target }) {
    const element = firstAncestorWithSpec(target);
    if (element) {
      this.onShow(element.__chromatic_spec__);
    }
  }

  onElementMouseOut({ target }) {
    const element = firstAncestorWithSpec(target);
    if (element) {
      this.onHide();
    }
  }

  onShow(activeSpec) {
    clearTimeout(this.timeout);
    this.setState({ activeSpec });
  }

  onHide() {
    clearTimeout(this.timeout);
    this.timeout = setTimeout(() => this.setState({ activeSpec: null }), 300);
  }

  render() {
    const { onCreateSpec, onShowComponentSpecs } = this.props;
    const { activeSpec } = this.state;
    hoverElement(activeSpec && activeSpec.domElement);

    if (activeSpec) {
      return (
        // NOTE: key is cruical here, it forces a re-render if the spec changes
        <Manager key={activeSpec.internalId}>
          <Target>{({ targetProps: { ref } }) => ref(activeSpec.domElement) || <div />}</Target>
          <Popper placement="top-start" style={{ position: 'absolute', zIndex: 2147483647 }}>
            <ScannerButtons
              spec={activeSpec}
              onMouseOver={() => this.onShow(activeSpec)}
              onMouseOut={this.onHide}
              onCreateSpec={() => onCreateSpec(activeSpec)}
              onShowComponentSpecs={onShowComponentSpecs}
            />
          </Popper>
        </Manager>
      );
    }
    return null;
  }
}

WidgetScanner.propTypes = {
  specs: PropTypes.arrayOf(
    PropTypes.shape({
      domElement: PropTypes.instanceOf(typeof Element !== 'undefined' ? Element : Object)
        .isRequired,
    })
  ).isRequired,
  onCreateSpec: PropTypes.func.isRequired,
  onShowComponentSpecs: PropTypes.func.isRequired,
};



