import React, { Component } from 'react';
import PropTypes from 'prop-types';
import CopyToClipboard from 'react-copy-to-clipboard';

import WithTooltip from '../tooltip/WithTooltip';
import TooltipNote from '../tooltip/TooltipNote';

class Clipboard extends Component {
  constructor(props) {
    super(props);
    this.state = { copied: false };
    this.onHideConfirmation = this.onHideConfirmation.bind(this);
    this.onShowConfirmation = this.onShowConfirmation.bind(this);
  }

  onHideConfirmation() {
    clearTimeout(this.timeout);
    const hide = () => this.setState({ copied: false });
    this.timeout = setTimeout(hide, 300);
  }

  onShowConfirmation() {
    clearTimeout(this.timeout);
    this.setState({ copied: true });
  }

  render() {
    const { children, toCopy, ...props } = this.props;
    return (
      <CopyToClipboard text={toCopy} onCopy={this.onShowConfirmation} {...props}>
        <WithTooltip
          placement="top"
          mode="hover"
          hasChrome={false}
          tooltip={<TooltipNote note={this.state.copied ? 'Copied' : 'Copy to clipboard'} />}
        >
          {children}
        </WithTooltip>
      </CopyToClipboard>
    );
  }
}

export default Clipboard;

Clipboard.propTypes = {
  children: PropTypes.node.isRequired,
  toCopy: PropTypes.string.isRequired,
};

Clipboard.defaultProps = {};



