import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { darken } from 'polished';
import { Icon, Spinner, WithTooltip, TooltipNote, styles } from '../../../../../lib/components';

const { color } = styles;

// prettier-ignore
const LogoWrapper = styled.span`
  transition: all 200ms ease-out;
  display: inline-block;
  vertical-align: top;
  padding: 3px 3px;
  border-radius: 3em;

  svg {
    display: block;
    height: 14px;
    width: 14px;

    path { fill: ${color.secondary} }
  }
`;

// prettier-ignore
const Button = styled.button`
  display: block;
	border: 0;
  box-shadow: 0 0 5px 0 rgba(0,0,0,0.30);
  border-radius: 3em;
  padding: 0;
	position: relative;
  outline: 0;
  cursor: pointer;
  transition: all 150ms ease-out;
  height: 20px;

  &:focus { outline:0; }

  ${props => props.testing && css`
    cursor: not-allowed !important;
    box-shadow: 0 0 5px 0 rgba(0,0,0,0.15);

    ${LogoWrapper} {
      opacity: .5;

      svg > path { fill: ${color.mediumdark}; }
    }
	`}

  ${LogoWrapper} {
    background: ${color.lightest};
  }

  ${props => !props.testing && !props.loading && css`
    &:hover {
      transform: translate3d(0,-1px,0);
      box-shadow: 0 0 7px 0 rgba(0,0,0,0.30);

      ${LogoWrapper} {
        box-shadow: #D9FAF7 0 0 3em 0 inset;

        svg path { fill: ${darken(0.05, color.secondary)}; }
      }
    }
  `}

  &:active { transform: translate3d(0,0,0); }

  ${props => props.loading && css`
    cursor: progress;

    ${LogoWrapper} svg { opacity: 0; }
  `}

`;

function WidgetButtonTest({ loading, testing, ...props }) {
  let tooltip = 'Starting test';
  if (!loading) {
    if (testing) {
      tooltip = 'Running test';
    } else {
      tooltip = 'Run test';
    }
  }
  return (
    <WithTooltip
      placement="top"
      mode="hover"
      hasChrome={false}
      tooltip={<TooltipNote note={tooltip} />}
    >
      <Button loading={loading} testing={testing} disabled={testing || loading} {...props}>
        <LogoWrapper>
          {loading && <Spinner inForm />}
          <Icon icon="play" />
        </LogoWrapper>
      </Button>
    </WithTooltip>
  );
}

WidgetButtonTest.propTypes = {
  loading: PropTypes.bool,
  testing: PropTypes.bool,
};

WidgetButtonTest.defaultProps = {
  loading: false,
  testing: false,
};

export default WidgetButtonTest;



