import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { observer } from 'mobx-react';

import {
  Icon,
  Button,
  ComponentHeader,
  Link,
  Subheading,
  SpecList,
  styles,
} from '../../../../../lib/components';

const { color, typography } = styles;

const Header = styled(ComponentHeader)``;

const Heading = styled(Subheading)`
  color: ${color.mediumdark};
  font-size: ${typography.size.s1 - 1}px;
  margin-bottom: 0.5rem;
  display: block;
`;

const EmptySpecsMessage = styled.div`
  font-size: ${typography.size.s2}px;
  line-height: 20px;
  margin: 0.75rem 0 1.5rem;
`;

const SavedSpecs = styled.div`margin-bottom: 1.5rem;`;
const UnsavedSpecs = styled.div``;

const Help = styled.div`
  font-size: ${typography.size.s1}px;
  color: ${color.dark};
  margin-top: 0.75rem;
  line-height: 1;
`;

// prettier-ignore
const Footer = styled.div`
  text-align: center;
`;

const HeaderWrapper = styled.div``;

// prettier-ignore
const Sidebar = styled.div`
  display: flex;
  flex-direction: column;

  ${HeaderWrapper} {
    flex: 1 1 auto;
    overflow: auto;
  }
  ${Footer} {
    flex: 0 0 auto;
    align-self: flex-end;
    width: 100%;
  }
`;

function SpecsSidebar({
  loading,
  component,
  selectedSpecId,
  onSelectSpec,
  onUpdateOrCreateSpec,
  onRemoveSpec,
  onHideComponentSpecs,
  ...other
}) {
  const { specs } = component;
  const savedSpecs = specs.filter(({ status }) => status === 'saved' || status === 'saving');
  const unsavedSpecs = specs.filter(({ status }) => status === 'unsaved');

  // SpecList deals in `id`, however we don't have one yet for unsaved specs.
  // We have an `internalId` (we don't want to call it `id` because that's
  // the server's `id`, which *does* exist for saved specs).
  const mapSpecs = ss => ss.map(spec => ({ ...spec, id: spec.internalId }));

  // XXX: make this a class I guess
  const onSave = (internalId, { name: specName, props }) => {
    const spec = specs.find(s => s.internalId === internalId);
    spec.name = specName;
    spec.input.element.props = {
      ...spec.input.element.props,
      ...props,
    };
    onUpdateOrCreateSpec(spec);
  };

  const onDelete = internalId => {
    onRemoveSpec(specs.find(s => s.internalId === internalId));
  };

  const actionButton = (
    <Button small icon onClick={onHideComponentSpecs}>
      <Icon icon="cross" />
    </Button>
  );

  return (
    <Sidebar {...other}>
      <HeaderWrapper>
        <Header loading={loading} component={component} actionButton={actionButton} />

        {loading && <SpecList loading />}

        {savedSpecs.length === 0 &&
        !loading && (
          <EmptySpecsMessage>
            Add a test spec by saving the current state of this component. You can change the spec
            at any time.{' '}
            <Link tertiary href="http://docs.chromaticqa.com/specs">
              <b>Read more</b>
            </Link>
          </EmptySpecsMessage>
        )}

        {savedSpecs.length > 0 && (
          <SavedSpecs>
            <Heading>Saved</Heading>
            <SpecList
              loading={loading}
              specs={mapSpecs(savedSpecs)}
              selectedId={selectedSpecId}
              onSelect={onSelectSpec}
              onSave={onSave}
              onDelete={onDelete}
            />
          </SavedSpecs>
        )}

        {unsavedSpecs.length > 0 && (
          <UnsavedSpecs>
            <Heading>Unsaved</Heading>
            <SpecList
              specs={mapSpecs(unsavedSpecs)}
              selectedId={selectedSpecId}
              onSelect={onSelectSpec}
              onSave={onSave}
            />
          </UnsavedSpecs>
        )}
      </HeaderWrapper>
      {!loading && (
        <Footer>
          {component.webUrl && (
            <Button small outline isLink href={component.webUrl} target="_blank">
              View in Chromatic
            </Button>
          )}
          <Help>
            <Link
              tertiary
              href="http://docs.chromaticqa.com/specs#troubleshooting-specs"
              target="_blank"
            >
              Doesn&rsquo;t look right?
            </Link>
          </Help>
        </Footer>
      )}
    </Sidebar>
  );
}

SpecsSidebar.propTypes = {
  loading: PropTypes.bool,
  component: PropTypes.shape({
    name: PropTypes.string.isRequired,
    webUrl: PropTypes.string,
    specs: PropTypes.arrayOf(
      PropTypes.shape({
        internalId: PropTypes.string.isRequired,
        name: PropTypes.string,
        input: PropTypes.any.isRequired,
        status: PropTypes.oneOf(['saving', 'saved', 'unsaved']).isRequired,
      })
    ).isRequired,
  }).isRequired,
  selectedSpecId: PropTypes.string,
  onSelectSpec: PropTypes.func.isRequired,
  onUpdateOrCreateSpec: PropTypes.func.isRequired,
  onRemoveSpec: PropTypes.func.isRequired,
  onHideComponentSpecs: PropTypes.func.isRequired,
};

SpecsSidebar.defaultProps = {
  loading: false,
  component: {
    id: 'loading',
    name: 'loading',
    specs: [],
    builds: [],
  },
  specs: SpecList.defaultProps.specs,
  selectedSpecId: 'loading',
};

export default observer(SpecsSidebar);



