import Spec from './Spec';

// All errors are unique errors
let errorCounter = 0;

export default class ErrorSpec extends Spec {
  constructor({ error, node, component = { name: node.name }, domElement }) {
    super({
      component,
      key: (errorCounter += 1).toString(),
      node,
      domElement,
    });

    this.error = error;
    this.status = 'unsaveable';
    this.unsaveableReason = error.toString();
  }
}



