import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { darken } from 'polished';
import { background, color, typography } from './../shared/styles';
import { inlineGlow } from './../shared/animation';
import Link from './Link';

// prettier-ignore
const Count = styled.div`
  color: ${color.dark};
  display: block;

  ${props => props.status === 'positive' && css`
    color: ${color.positive};
	`};
  ${props => props.status === 'negative' && css`
    color: ${color.negative};
	`};
  ${props => props.status === 'warning' && css`
    color: ${color.warning};
	`};
  ${props => props.status === 'neutral' && css`
    color: ${color.mediumdark};
	`};
  ${props => props.status === 'link' && css`
    color: ${color.primary};
    text-decoration: none;
    &:hover { color: ${darken(0.07, color.primary)};}
    &:active { color: ${darken(0.10, color.primary)};}
	`};

  span {
    display: inline-block;
  }

`;

const Text = styled.div`
  span {
    display: inline-block;
  }
`;

// prettier-ignore
const CardinalInner = styled.div`
  display: inline-block;
  text-align: ${props => props.size === 'small' ? 'left' : 'center'};
  vertical-align: top;
  padding: 8px 12px;
  border-radius: 4px;

  ${props => props.loading && css`
    ${Count}, ${Text} {
      overflow: hidden;

      > span, a {
        ${inlineGlow};
      }
    }
	`};

  ${props => props.selectable && css`
    cursor: pointer;

    &:hover {
      background: ${background.app};
    }
	`};

  ${props => props.active && css`
    background: ${background.app};
	`};

  ${Count} {
    font-weight: ${props => props.size === 'small' ? typography.weight.bold : typography.weight.regular};
    font-size: ${props => props.size === 'small' ? typography.size.s2 : typography.size.l1}px;
    line-height: ${props => props.size === 'small' ? typography.size.s3 : typography.size.l1}px;
    margin-bottom: 2px;
  }

  ${Text} {
    color: ${props => props.size === 'small' ? color.mediumdark : color.dark};
    font-size: ${props => props.size === 'small' ? typography.size.s1 : typography.size.s2}px;
    line-height: ${typography.size.s2}px;
    clear: both;
  }
`;

function Cardinal({
  loading,
  selectable,
  onHover,
  onClick,
  active,
  size,
  count,
  countLink,
  text,
  status,
  ...props
}) {
  let countValue = count;
  if (countLink) {
    countValue = <Link href={countLink}>{count}</Link>;
  }
  let events = {};
  if (selectable) {
    events = {
      onMouseEnter: () => onHover(true),
      onMouseLeave: () => onHover(false),
      onClick,
    };
  }

  return (
    <CardinalInner
      loading={loading}
      selectable={selectable}
      active={active}
      size={size}
      {...events}
      {...props}
    >
      <Count status={status}>
        <span>{countValue}</span>
      </Count>
      <Text>
        <span>{text}</span>
      </Text>
    </CardinalInner>
  );
}

Cardinal.propTypes = {
  loading: PropTypes.bool,
  selectable: PropTypes.bool,
  onHover: PropTypes.func,
  onClick: PropTypes.func,
  active: PropTypes.bool,
  size: PropTypes.oneOf(['small', 'large']),
  count: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  countLink: PropTypes.string,
  text: PropTypes.string,
  status: PropTypes.oneOf(['default', 'positive', 'negative', 'warning', 'neutral', 'link']),
};

Cardinal.defaultProps = {
  loading: false,
  selectable: false,
  onHover: () => 0,
  onClick: () => 0,
  active: false,
  size: 'large',
  status: 'default',
  count: '000',
  countLink: null,
  text: 'loading',
};

export default Cardinal;



