import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { color } from './../shared/styles';
import { rotate360, glow } from './../shared/animation';

// prettier-ignore
const Status = styled.div`
  background-color: ${color.mediumlight};
  border-radius: 3em;
  cursor: ${props => props.progress ? 'progress' : 'inherit' };
  display: inline-block;
  overflow: hidden;
  position: relative;
  transition: all 200ms ease-out;
  transform: translate3d(0,0,0);
  vertical-align: top;

  &:before {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    content: '';
    border-radius: 3em;
    z-index: 1;
  }

  /* dimensions */
  height: 44px;
  width: 44px;

  ${props => props.size === 'medium' && css`
    height: 32px;
    width: 32px;
	`}

  ${props => props.size === 'small' && css`
    height: 20px;
    width: 20px;
	`}

  ${props => props.status === 'loading' && css`
    animation: ${glow} 1.5s ease-in-out infinite;
	`}

  /* background */
  ${props => props.status === 'positive' && css`
    background-color: ${color.positive};
    background-image: linear-gradient(20deg, #74E48B 0%, #61D135 100%);

    &:before {
      background-image: radial-gradient(50% 100%, rgba(0,189,255,0.30) 14%, rgba(0,0,0,0.00) 100%);
    }
	`}

  ${props => props.status === 'negative' && css`
    background-color: ${color.negative};
    background-image: linear-gradient(22deg, #EB7E44 0%, #FF4400 100%), linear-gradient(-245deg, rgba(255,219,0,0.30) 5%, rgba(0,0,0,0.00) 100%);
	`}

  ${props => props.status === 'warning' && css`
    background-color: ${color.warning};
    background-image: linear-gradient(20deg, #FED230 0%, #FEBB30 100%);

    &:before {
      background-image: radial-gradient(50% 100%, rgba(255,133,0,0.30) 14%, rgba(0,0,0,0.00) 100%);
    }
	`}

  ${props => props.status === 'error' && css`
    background-color: transparent;
    border: 1px solid ${color.mediumdark};

    &:before {
      background: ${color.mediumdark};
      border-radius: 3em;
      position: absolute;
      top: 20%;
      left: calc(50% - 1px);
      height: 45%;
      width: 2px;
    }

    &:after {
      background: ${color.mediumdark};
      border-radius: 3em;
      content: '';
      position: absolute;
      bottom: 20%;
      left: calc(50% - 1px);
      height: 2px;
      width: 2px;
    }
	`}

  ${props => props.status === 'progress' && css`
    animation: ${rotate360} .7s linear infinite;
    background: transparent;
    border-width: ${props.size === 'small' ? '1px' : '2px' };
    border-style: solid;
    border-left-color: #00AAFF;
    border-bottom-color: #25CCFD;
    border-right-color: #58FAF9;
    border-top-color: transparent;
	`}
`;

function StatusLight({ status, className, size, ...props }) {
  return <Status status={status} className={className} size={size} {...props} />;
}

StatusLight.propTypes = {
  status: PropTypes.oneOf(['positive', 'negative', 'warning', 'loading', 'progress', 'error']),
  size: PropTypes.oneOf(['small', 'medium', 'large']),
  className: PropTypes.string,
};

StatusLight.defaultProps = {
  status: 'loading',
  size: 'large',
  className: null,
};

export default StatusLight;



