import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { Link, ProgressDots, StatusLight, styles, formatting } from '../../../../../lib/components';

const { color, typography } = styles;
const { formatBuildBadge, formatBuildStatus } = formatting;

// prettier-ignore
const Title = styled.b``;
const Desc = styled.span``;

// prettier-ignore
const Links = styled.div`
  margin-top: 8px;
  text-align: center;

  > * {
    margin: 0 8px;
    font-weight: ${typography.weight.extrabold};
  }
`;

// prettier-ignore
const Message = styled.div`
  color: ${color.darker};
  line-height: 18px;
  overflow-wrap: break-word;
  hyphens: auto;
`;

// prettier-ignore
const Figure = styled.div`
  margin-top: 8px;
  margin-left: -15px;
  margin-right: -15px;
  position: relative;

  img {
    width: 100%;
    display: block;
    height: auto;
  }

  span {
    display: inline-block;
    position: absolute;
    font-size: 10px;
    line-height: 24px;
    bottom: -24px;
    right: 10px;
    color: ${color.mediumdark};
  }

`;

// prettier-ignore
const Progress = styled(ProgressDots)`
  display: block;
  text-align: center;
  line-height: 0;
  margin-bottom: -5px;
`;

// prettier-ignore
const BuildLink = styled(Link)`
  font-weight: ${typography.weight.extrabold};
  position: absolute;
  right: 0;
  top: 0;
  line-height: 30px;
`;

// prettier-ignore
const BuildStatus = styled(StatusLight)`
  float: left;
  margin-top: 4px;
  margin-right: 10px;
`;

// prettier-ignore
const Build = styled.div`
  position: relative;
  overflow: visible;
`;

// prettier-ignore
const Meta = styled.div`
  overflow: hidden;
`;

// prettier-ignore
const Name = styled.div`
  font-weight: ${typography.weight.extrabold};
`;

// prettier-ignore
const Details = styled.div`
  font-size: 10px;
`;

// prettier-ignore
const NotificationWrapper = styled.div`
  background-image: linear-gradient(-1deg, rgba(248,248,248,0.97) 0%, rgba(247,252,255,0.97) 100%);
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.05), 0 5px 15px 0 rgba(0,0,0,0.10);
  border-radius: 4px;
  font-size: ${typography.size.s1}px;
  padding: 15px;
  width: 280px;
  box-sizing: border-box;

  ${Title} + ${Desc} { margin-left: 4px; }
  ${Links} + ${Progress} {
    margin-top: 8px;
  }
  ${Message} + ${Progress} {
    margin-top: 12px;
  }
  ${Figure} + ${Progress} {
    margin-top: 10px;
  }

  code {
    font-size: ${typography.size.s1 - 1}px;
  }
`;

function WidgetNotification({ title, desc, links, progress, figure, status, build, ...props }) {
  return (
    <NotificationWrapper {...props}>
      {(title || desc) && (
        <div>
          <Message>
            <Title>{title}</Title>
            <Desc>{desc}</Desc>
          </Message>
          {links && (
            <Links>
              {links.map(({ title: linkTitle, ...other }) => (
                <Link {...other} key={linkTitle}>
                  {linkTitle}
                </Link>
              ))}
            </Links>
          )}
          {figure && (
            <Figure>
              <img alt="" src={figure} />
              <span>Try it to continue</span>
            </Figure>
          )}
          {progress.length !== 0 && <Progress progress={progress[0]} steps={progress[1]} />}
        </div>
      )}
      {build && (
        <Build>
          <BuildStatus
            size="small"
            status={formatBuildBadge(build.status)}
            title={formatBuildStatus(build.status)}
          />
          <Meta>
            <Name>Build {build.number}</Name>
            <Details>
              {build.specCount} specs - {build.componentCount} components
            </Details>
          </Meta>

          <BuildLink href={build.webUrl} target="_blank">
            View
          </BuildLink>
        </Build>
      )}
    </NotificationWrapper>
  );
}

WidgetNotification.propTypes = {
  title: PropTypes.string,
  desc: PropTypes.node,
  links: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string.isRequired,
    })
  ),
  progress: PropTypes.arrayOf(PropTypes.number),
  figure: PropTypes.string,
  status: PropTypes.string,
  build: PropTypes.shape({
    number: PropTypes.number.isRequired,
    status: PropTypes.string.isRequired,
    componentCount: PropTypes.number.isRequired,
    specCount: PropTypes.number.isRequired,
    webUrl: PropTypes.string.isRequired,
  }),
};

WidgetNotification.defaultProps = {
  title: null,
  desc: null,
  links: null,
  progress: [],
  figure: null,
  status: null,
  build: null,
};

export default WidgetNotification;



