import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { easing } from './../shared/animation';

import Button from './Button';

// prettier-ignore
const Rest = styled.span`
  transform: translate3d(0,0,0);
  display: inline-block;
  opacity: 1;
`

// prettier-ignore
const Hover = styled.span`
  transform: translate3d(0, 100%, 0);
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  opacity: 0;
`;

// prettier-ignore
const ButtonHoverInner = styled(Button)`
  ${Rest}, ${Hover} { transition: all 700ms ${easing.rubber}; }

  &:hover {
    ${Rest} {
      transform: translate3d(0,-100%,0);
      opacity: 0;
    }
    ${Hover} {
      transform: translate3d(0, -50%,0);
      opacity: 1;
    }
  }

`;

export default function ButtonHover({ text, active, ...props }) {
  const restText = text[0];
  const hoverText = text[1];

  return (
    <ButtonHoverInner active={active} {...props}>
      <Rest>{restText}</Rest>
      <Hover>{hoverText}</Hover>
    </ButtonHoverInner>
  );
}

ButtonHover.propTypes = {
  text: PropTypes.arrayOf(PropTypes.string.isRequired).isRequired,
  active: PropTypes.bool,
};
ButtonHover.defaultProps = {
  active: false,
};



