import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import highlightjs from 'highlight.js';
import { color } from './../shared/styles';

// NOTE: this is the full styles, requirable at `highlight.js/styles/default.css`
// If/when we know this package is only used inside webpack, we could use CSS-loader
// to require that directly, rather than reproducing it here
// prettier-ignore
const StyledHighlight = styled.code`
  color: ${color.darker};
  overflow-wrap: break-word;
  max-width: 100%;
  
  .hljs{display:block;background:white;padding:0.5em;color:#333333;overflow-x:auto}.hljs-comment,.hljs-meta{color:#969896}.hljs-string,.hljs-variable,.hljs-template-variable,.hljs-strong,.hljs-emphasis,.hljs-quote{color:#df5000}.hljs-keyword,.hljs-selector-tag,.hljs-type{color:#a71d5d}.hljs-literal,.hljs-symbol,.hljs-bullet,.hljs-attribute{color:#0086b3}.hljs-section,.hljs-name{color:#63a35c}.hljs-tag{color:#333333}.hljs-title,.hljs-attr,.hljs-selector-id,.hljs-selector-class,.hljs-selector-attr,.hljs-selector-pseudo{color:#795da3}.hljs-addition{color:#55a532;background-color:#eaffea}.hljs-deletion{color:#bd2c00;background-color:#ffecec}.hljs-link{text-decoration:underline}
`;

export default class Highlight extends Component {
  componentDidMount() {
    this.highlightCode();
  }

  componentDidUpdate() {
    this.highlightCode();
  }

  highlightCode() {
    if (!this.code.noHighlight) {
      highlightjs.highlightBlock(this.code);
    }
  }

  render() {
    const { language, children, ...props } = this.props;

    return (
      <pre {...props}>
        <StyledHighlight
          className={language}
          innerRef={r => {
            this.code = r;
          }}
        >
          {children}
        </StyledHighlight>
      </pre>
    );
  }
}

Highlight.propTypes = {
  children: PropTypes.node.isRequired,
  language: PropTypes.string,
};

Highlight.defaultProps = {
  language: null,
};



