import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { withState } from 'recompose';

import Icon from './../basics/Icon';
import Badge from './../basics/Badge';
import Button from './../basics/Button';
import Link from './../basics/Link';
import Radio from './../forms/Radio';
import StatusLight from './../basics/StatusLight';
import SpecEditor from './SpecEditor';

import { color, typography } from './../shared/styles';
import { glow, inlineGlow, rotate360 } from './../shared/animation';

// prettier-ignore
const Saving = styled.div`
  color: ${color.mediumdark};
  font-size: ${typography.size.s1}px;
  line-height: 14px;
  padding: 7px 0;

  svg {
    animation: ${rotate360} 1200ms linear infinite;
    height: 12px;
    width: 12px;
    margin-right: .4em;
    vertical-align: top;
    margin-top: 1px;

    path { fill: ${color.mediumdark}; }
  }
`;

// prettier-ignore
const ReviewStatus = styled(StatusLight)`
  height: 6px;
  width: 6px;
  display: inline-block;
  vertical-align: top;
  margin-right: 4px;
  margin-top: 4px;
`;

// prettier-ignore
const Review = styled(Link)`
  font-size: ${typography.size.s1}px;
  font-weight: ${typography.weight.bold};
  line-height: 14px;
  padding: 7px 0;
`;

const DeleteBadge = styled(Badge)`margin-top: 4px;`;

// prettier-ignore
const Spec = styled(Radio)`
  input {
    margin-right: 10px;
  }

  input:checked + span {
    font-weight: ${typography.weight.bold};
    color: ${color.primary};
  }

  > span {
    font-weight: ${typography.weight.regular};
  }
`;

// prettier-ignore
const Action = styled(Button)`
  opacity: 0;
  color: ${color.mediumdark};
  font-weight: ${typography.weight.bold};

  path {
    transition: all 150ms ease-in-out;
    fill: ${color.mediumdark};
  }

  &:hover {
    color: ${color.dark};
    path { fill: ${color.dark}; }
  }
`;

// prettier-ignore
const Actions = styled.div`
  position: absolute;
  right: 0;
  top: 0;
`;

// prettier-ignore
const Item = styled.div`
  position: relative;
  padding: 7px 0;

  ${props => props.loading && css`
    ${Spec} > div {
      ${inlineGlow};
      float: left;

      &:before {
        animation: ${glow} 1.5s ease-in-out infinite;
        box-shadow: none;
        background: ${color.medium};
      }
    }
  `}

  &:hover ${Action} {
    opacity: 1;
  }

  ${props => props.status === 'unsaved' && props.selected && css`
    ${Action} {
      opacity: 1;
    }
  `}
`;

const Editor = styled(SpecEditor)`margin-top: 0.75rem;`;

function SpecItem({
  loading,
  spec,
  index,
  reviewLink,
  selected,
  onSelect,
  onSave,
  onDelete,
  editing,
  onEdit,
  ...props
}) {
  const { id, name, number, status, ignoredSelectors } = spec;
  const saving = status === 'saving';
  const ourName =
    name ||
    (status === 'saved' || status === 'deleted' ? `Spec #${number}` : `Unsaved #${index + 1}`);

  let actionContents;

  if (reviewLink) {
    actionContents = (
      <Review href={reviewLink} secondary>
        <ReviewStatus status="warning" />
        Review
      </Review>
    );
  } else if (saving) {
    actionContents = (
      <Saving>
        <Icon icon="sync" />Saving
      </Saving>
    );
  } else if (status === 'deleted') {
    actionContents = <DeleteBadge status="neutral">Deleted</DeleteBadge>;
  } else if (onSave) {
    // Corresponds to SpecEditor being open
    if (editing) {
      actionContents = (
        <Action icon small onClick={() => onEdit(false)} title="Cancel">
          <Icon icon="close" />
        </Action>
      );
    } else if (onDelete) {
      // onDelete signals that a spec is Saved
      actionContents = (
        <Action icon small onClick={() => onEdit(true)} title="Edit">
          <Icon icon="edit" />
        </Action>
      );
    } else {
      actionContents = (
        <span>
          <Action icon small onClick={() => onEdit(true)} title="Edit">
            <Icon icon="edit" />
          </Action>
          <Action small icon onClick={onSave} title="Save">
            Save
          </Action>
        </span>
      );
    }
  }

  const onSaveAndClose = item => {
    onSave(item);
    onEdit(false);
  };
  return (
    <Item loading={loading} status={status} selected={selected} {...props}>
      <Spec
        label={ourName}
        sublabel={
          ignoredSelectors && ignoredSelectors.length > 0 ? (
            `${ignoredSelectors.length} ignored selectors`
          ) : null
        }
        name="spec"
        value={id}
        checked={selected}
        onChange={onSelect}
      />
      {!loading && <Actions>{actionContents}</Actions>}
      {editing && (
        <Editor
          spec={spec}
          onSave={onSaveAndClose}
          onDelete={onDelete}
          onCancel={() => onEdit(false)}
        />
      )}
    </Item>
  );
}

SpecItem.propTypes = {
  loading: PropTypes.bool,
  spec: PropTypes.shape({
    id: PropTypes.string.isRequired,
    name: PropTypes.string,
    number: PropTypes.number,
    status: PropTypes.oneOf(['unsaved', 'saving', 'saved', 'deleted']),
    ignoredSelectors: PropTypes.arrayOf(PropTypes.string.isRequired),
  }),
  index: PropTypes.number.isRequired,
  selected: PropTypes.bool,
  onSelect: PropTypes.func.isRequired,
  onSave: PropTypes.func,
  onDelete: PropTypes.func,
  reviewLink: PropTypes.string,
  editing: PropTypes.bool.isRequired,
  onEdit: PropTypes.func.isRequired,
};

SpecItem.defaultProps = {
  loading: false,
  spec: {
    id: 'loading',
    name: 'Loading spec',
    status: 'unsaved',
    ignoredSelectors: [],
  },
  selected: false,
  reviewLink: null,
  onSave: null,
  onDelete: null,
};

export default withState('editing', 'onEdit', false)(SpecItem);



