/* eslint-env browser */
import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { observer } from 'mobx-react';
import { compose, withState } from 'recompose';
import { Canvas, styles } from '../../../../../lib/components';

import SpecsSidebar from './SpecsSidebar';
import OverlayLayout from '../layouts/OverlayLayout';

const { color } = styles;

const Sidebar = styled(SpecsSidebar)``;
const Content = styled(Canvas)``;
const ContentWrapper = styled.div``;

// prettier-ignore
const PageWrapper = styled.div`
  border-bottom: 1px solid ${color.mediumlight};
  display: flex;
  position: relative;

  ${Sidebar} {
    flex: 0 0 auto;
    width: 25%;
    padding: 30px 20px 20px;
  }

  ${ContentWrapper} {
    flex: 1 1 auto;
    position: relative;
  }

  ${Sidebar}, ${ContentWrapper} {
    height: calc(100vh - 1px);
  }

  ${Content} {
    left: 0;
    right: 10px;
    top: 10px;
    bottom: 10px;
    position: absolute;
    display: flex;
  }
`;

function SpecsScreen({
  loading,
  component,
  selectedSpecInternalId,
  onSelectSpecInternal,
  onUpdateOrCreateSpec,
  onRemoveSpec,
  onHideComponentSpecs,
}) {
  const selectedSpec =
    component.specs.find(s => s.internalId === selectedSpecInternalId) || component.specs[0];
  const isolatorUrl = `${document.location.protocol}//${document.location.host}`;

  return (
    <OverlayLayout>
      <PageWrapper>
        <Sidebar
          loading={loading}
          component={component}
          specs={component.specs}
          selectedSpecId={selectedSpec && selectedSpec.internalId}
          onSelectSpec={onSelectSpecInternal}
          onUpdateOrCreateSpec={onUpdateOrCreateSpec}
          onRemoveSpec={onRemoveSpec}
          onHideComponentSpecs={onHideComponentSpecs}
        />

        <ContentWrapper>
          <Content loading={loading} interactive figure={isolatorUrl} spec={selectedSpec} />
        </ContentWrapper>
      </PageWrapper>
    </OverlayLayout>
  );
}

SpecsScreen.propTypes = {
  loading: PropTypes.bool,
  component: PropTypes.shape({
    name: PropTypes.string.isRequired,
    webUrl: PropTypes.string,
    specs: PropTypes.arrayOf(
      PropTypes.shape({
        internalId: PropTypes.string.isRequired,
        name: PropTypes.string,
        input: PropTypes.any.isRequired,
        status: PropTypes.oneOf(['saving', 'saved', 'unsaved']).isRequired,
      })
    ).isRequired,
  }),
  selectedSpecInternalId: PropTypes.string,
  onSelectSpecInternal: PropTypes.func.isRequired,
  onUpdateOrCreateSpec: PropTypes.func.isRequired,
  onRemoveSpec: PropTypes.func.isRequired,
  onHideComponentSpecs: PropTypes.func.isRequired,
};

SpecsScreen.defaultProps = {
  component: { name: 'loading', specs: [] },
  selectedSpecInternalId: null,
  loading: false,
  build: {
    id: 'loading',
    number: null,
    commit: 'loading',
  },
};

export default compose(
  observer,
  withState(
    'selectedSpecInternalId',
    'onSelectSpecInternal',
    ({ component, specInternalId }) =>
      specInternalId || (component && component.specs.length > 0 && component.specs[0].internalId)
  )
)(SpecsScreen);



