/* eslint-env browser */
import { connectToDevTools } from 'react-devtools-core';
import { observable } from 'mobx';
import MockWebSocket from './MockWebSocket';

export const detectedReact = observable(false);

// We need to set this stuff up *RIGHT AWAY* before react gets imported.
// So we can't even wait for the `listen` call
const oldWS = window.WebSocket;
window.WebSocket = MockWebSocket;
connectToDevTools();
window.WebSocket = oldWS;

// eslint-disable-next-line no-underscore-dangle
const hook = window.__REACT_DEVTOOLS_GLOBAL_HOOK__;

let listening = false;
const renderers = {};
hook.sub('renderer-attached', ({ id, renderer, helpers }) => {
  detectedReact.set(true);
  if (!listening) {
    // We need to force devtools to re-attach these renderers later
    renderers[id] = { id, renderer, helpers };
  }
});

export default function listen(onNode) {
  // eslint-disable-next-line no-underscore-dangle
  window.__REACT_DEVTOOLS_GLOBAL_HOOK__.on('react-devtools', agent => {
    listening = true;
    agent.on('mount', node => {
      const element = agent.getNodeForID(node.id);

      // Need to wait for the element to get attached to the DOM
      setTimeout(() => {
        onNode({ node, element, event: 'mount' });
      }, 0);
    });

    agent.on('update', node => {
      const element = agent.getNodeForID(node.id);

      onNode({ node, element, event: 'update' });
    });

    agent.on('unmount', nodeId => {
      onNode({ nodeId, event: 'unmount' });
    });

    // Fake re-attaching all renderers. This triggers the backend to walk the
    // tree, emitting "mount" events on the agent
    Object.values(renderers).forEach(renderer => {
      hook.emit('renderer-attached', renderer);
    });
  });
}



