import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { darken } from 'polished';
import { color } from './../shared/styles';

// prettier-ignore
const LinkWrapper = styled.a`
  display: inline-block;
  transition: all 150ms ease-out;
  text-decoration: none;

  color: ${color.primary};
  > svg path { fill: ${color.primary}; }
  &:hover, &:focus {
    cursor: pointer;
    transform: translate3d(0,-1px,0);
    color: ${darken(0.07, color.primary)};
    > svg path { fill: ${darken(0.07, color.primary)} }
  }
  &:active {
    transform: translate3d(0,0,0);
    color: ${darken(0.10, color.primary)};
    > svg path { fill: ${darken(0.10, color.primary)} }
  }

  > svg {
    display: inline-block;
    height: .85em;
    width: .85em;
    vertical-align: text-top;
    position: relative;
    bottom: -.225em;
    margin-right: .25em;
  }

  ${props => props.icon && css`
    svg {
      height: 1em;
      width: 1em;
      vertical-align: middle;
      position: relative;
      bottom: 0;
      margin-right: 0;
    }
  `}

  ${props => props.secondary && css`
    color: ${color.mediumdark};
    > svg path { fill: ${color.mediumdark}; }

    &:hover {
      color: ${color.dark};
      > svg path { fill: ${color.dark}; }
    }

    &:active {
      color: ${color.darker};
      > svg path { fill: ${color.darker}; }
    }
	`}

  ${props => props.tertiary && css`
    color: ${color.dark};
    > svg path { fill: ${color.dark}; }

    &:hover {
      color: ${color.darkest};
      > svg path { fill: ${color.darkest}; }
    }

    &:active {
      color: ${color.mediumdark};
      > svg path { fill: ${color.mediumdark}; }
    }
	`}

  ${props => props.nochrome && css`
    color: inherit;

    &:hover, &:active {
      color: inherit;
      text-decoration: underline;
    }
	`}

  ${props => props.inverse && css`
    color: ${color.lightest};
    > svg path { fill: ${color.lightest}; }

    &:hover {
      color: ${color.lighter};
      > svg path { fill: ${color.lighter}; }
    }

    &:active {
      color: ${color.light};
      > svg path { fill: ${color.light}; }
    }
	`}

  ${props => props.isButton && css`
    border: 0;
    background: none;
    padding: 0;
    font-size: inherit;

    &:focus {
      border-bottom: 1px solid;
    }
  `}
`;

const LinkButton = LinkWrapper.withComponent('button');

function Link({ isButton, ...props }) {
  if (isButton) {
    return <LinkButton isButton {...props} />;
  }
  return <LinkWrapper {...props} />;
}

Link.propTypes = {
  isButton: PropTypes.bool,
  children: PropTypes.node,
};

Link.defaultProps = {
  isButton: false,
  children: null,
};

export default Link;



