import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import Link from './../basics/Link';
import Button from './../basics/Button';
import Icon from './../basics/Icon';
import WithTooltip from './../tooltip/WithTooltip';
import { color, typography } from './../shared/styles';
import { inlineGlow } from './../shared/animation';

// prettier-ignore
const Title = styled.h1`
  && {
    font-weight: ${typography.weight.extrabold};
    font-size: ${typography.size.m1}px;
    line-height: 1;
    padding: 4px 28px 4px 0;
    overflow-wrap: break-word;
    hyphens: auto;
  }
`;

const BuildListButton = styled(Button)`
  margin-left: 4px;
  padding: 2px;

  svg {
    height: 12px;
    width: 12px;
  }
`;

// prettier-ignore
const Detail = styled(Link)`
  color: ${color.dark};
  font-weight: ${typography.weight.bold};
  font-size: ${typography.size.s2 - 1}px;
  line-height: 1;
  line-height: 18px;
`;

// prettier-ignore
const CommitLink = styled(Link)`
  font-size: ${typography.size.s1 - 1}px;
  margin-left: 12px;
  display: inline-block;
  line-height: 18px;
`;

const DetailTooltip = styled(WithTooltip)`
  display: inline-block;
  vertical-align: top;
`;

const DetailWrapper = styled.div`
  font-size: 0;
  margin-top: 2px;
`;

const Action = styled.div``;

// prettier-ignore
const Meta = styled.div`
  font-size: 0;

  ${Action} {
    position: absolute;
    right: 0;
    top: 0;
  }

  ${props => props.loading  && css`
    ${Title} span, ${Detail} {
      ${inlineGlow}
    }
  `}
`;

// prettier-ignore
const Break = styled.div`
  border-top: 1px solid ${color.mediumlight};
  margin: .75rem 0 .75rem;
`;

// prettier-ignore
const Header = styled.div`
  position: relative;
`;

export default function ComponentHeader({
  loading,
  build,
  component: { name },
  buildTooltip,
  actionButton,
  LinkWrapper,
  ...props
}) {
  let detail;
  if (loading) {
    detail = <Detail>Loading</Detail>;
  }
  if (!loading && build) {
    const { number, commit, commitUrl, webUrl } = build;

    let detailInner = (
      <Detail tertiary href={webUrl}>
        Build {number}
      </Detail>
    );
    if (LinkWrapper) {
      detailInner = <LinkWrapper href={webUrl}>{detailInner}</LinkWrapper>;
    }

    if (buildTooltip) {
      detailInner = (
        <span>
          {detailInner}
          <DetailTooltip placement="bottom" mode="click" tooltip={buildTooltip}>
            <BuildListButton small icon outline>
              <Icon icon="arrowdown" />
            </BuildListButton>
          </DetailTooltip>
        </span>
      );
    }

    detail = (
      <DetailWrapper>
        {detailInner}
        {commit && (
          <CommitLink href={commitUrl} tertiary target="_blank">
            {commit.substring(0, 6)}
          </CommitLink>
        )}
      </DetailWrapper>
    );
  }
  return (
    <Header loading={loading} {...props}>
      <Meta loading={loading}>
        <Title>
          <span>{name}</span>
        </Title>
        {detail}

        {!loading && <Action>{actionButton}</Action>}
      </Meta>
      <Break />
    </Header>
  );
}

ComponentHeader.propTypes = {
  loading: PropTypes.bool,
  build: PropTypes.shape({
    id: PropTypes.string.isRequired,
    webUrl: PropTypes.string.isRequired,
    number: PropTypes.number.isRequired,
    commit: PropTypes.string,
    commitUrl: PropTypes.string,
  }),
  component: PropTypes.shape({
    name: PropTypes.string,
  }),
  buildTooltip: PropTypes.node,
  actionButton: PropTypes.node,
  LinkWrapper: PropTypes.func,
};

ComponentHeader.defaultProps = {
  loading: false,
  build: null,
  component: {
    name: 'loading',
    specs: [],
    builds: [],
  },
  buildTooltip: false,
  actionButton: null,
  LinkWrapper: null,
};



