import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class Wobbler extends Component {
  constructor() {
    super();
    this.state = { wobble: false };
    this.wobble = this.wobble.bind(this);
  }

  wobble() {
    this.setState({ wobble: Math.random() });
  }

  render() {
    const { Animator, duration, children, ...props } = this.props;
    const { wobble } = this.state;
    if (wobble) {
      return (
        <Animator key={wobble} duration={duration}>
          {children}
        </Animator>
      );
    }
    return <div {...props}>{children}</div>;
  }
}

Wobbler.propTypes = {
  Animator: PropTypes.func.isRequired,
  duration: PropTypes.string,
  children: PropTypes.node.isRequired,
};

Wobbler.defaultProps = {
  duration: null,
};



