import React, { Component } from 'react';
import PropTypes from 'prop-types';
import ApolloClient, { createNetworkInterface } from 'apollo-client';
import { ApolloProvider } from 'react-apollo';
import gql from 'graphql-tag';
import styled from 'styled-components';

import WidgetScreen from './screens/WidgetScreen';
import WidgetPage from './pages/WidgetPage';
import SpecsPage from './pages/SpecsPage';
import { global } from '../../../../lib/components';

const { bodyStyles } = global;

const StyleWrapper = styled.div`${bodyStyles};`;

class App extends Component {
  constructor(props) {
    super(props);

    this.token = null;

    const networkInterface = createNetworkInterface({
      uri: `${this.props.indexUrl}/graphql`,
    });

    networkInterface.use([
      {
        applyMiddleware: (req, next) => {
          if (this.token) {
            req.options.headers = {
              ...req.options.headers,
              authorization: `bearer ${this.token}`,
            };
          }
          next();
        },
      },
    ]);

    this.apollo = new ApolloClient({
      networkInterface,
    });

    // While we are fetching our token, we just render loading
    this.state = {
      Page: WidgetScreen,
      pageProps: { loading: true },
      active: false,
    };

    this.onShowComponentSpecs = this.onShowComponentSpecs.bind(this);
    this.onHideComponentSpecs = this.onHideComponentSpecs.bind(this);
    this.onActivate = this.onActivate.bind(this);
  }

  async componentWillMount() {
    const { appCode } = this.props;
    if (!appCode) {
      this.setState({
        Page: WidgetScreen,
        pageProps: { criticalError: 'no-app-code' },
      });
      return;
    }

    try {
      const { data: { createAppToken } } = await this.apollo.mutate({
        mutation: gql`
          mutation WidgetCreateAppTokenMutation($code: String!) {
            createAppToken(code: $code)
          }
        `,
        variables: {
          code: this.props.appCode,
        },
      });
      this.token = createAppToken;
      this.setState({
        Page: WidgetPage,
        pageProps: {},
      });
    } catch (e) {
      // XXX: should check the error type here
      this.setState({
        Page: WidgetScreen,
        pageProps: { criticalError: 'bad-app-code' },
      });
    }
  }

  onShowComponentSpecs(componentName, specInternalId) {
    this.setState({
      Page: SpecsPage,
      pageProps: { componentName, specInternalId },
    });
  }

  onHideComponentSpecs() {
    this.setState({
      Page: WidgetPage,
      pageProps: {},
    });
  }

  onActivate(active) {
    this.setState({ active });
  }

  render() {
    const { appCode, isolatorOptions, detectedReact, specs } = this.props;
    const { Page, pageProps, active } = this.state;

    const detectedSpecs = specs.filter(s => s.status !== 'inactive');

    return (
      <ApolloProvider client={this.apollo}>
        <StyleWrapper>
          <Page
            {...pageProps}
            appCode={appCode}
            isolatorOptions={isolatorOptions}
            detectedReact={detectedReact}
            detectedSpecs={detectedSpecs}
            active={active}
            onShowComponentSpecs={this.onShowComponentSpecs}
            onHideComponentSpecs={this.onHideComponentSpecs}
            onActivate={this.onActivate}
          />
        </StyleWrapper>
      </ApolloProvider>
    );
  }
}

App.propTypes = {
  indexUrl: PropTypes.string.isRequired,
  appCode: PropTypes.string.isRequired,
  isolatorOptions: PropTypes.shape({
    noTunnel: PropTypes.bool.isRequired,
    port: PropTypes.number.isRequired,
  }).isRequired,
  detectedReact: PropTypes.bool.isRequired,
  specs: PropTypes.arrayOf(PropTypes.shape({}).isRequired).isRequired,
};

export default App;



