import React from 'react';
import styled, { keyframes } from 'styled-components';

const variables = {
  size: 200,
  bgBlue: 'radial-gradient(transparent, #58FAF9, #25CBFC)',
  duration: 8,
  delay: 2,
};

const radar = keyframes`
  0% {
      transform: scale(0);
      opacity: 0.2;
  }

  100% {
      transform: scale(2);
      opacity: 0;
  }
`;

const Dot = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  height: 50%;
  width: 50%;
  background: ${variables.bgBlue};
  border-radius: 50%;
  transform: scale(0);
  opacity: 0;
  animation: ${radar} ${variables.duration}s infinite cubic-bezier(0, 0, 0.46, 1);

  &:nth-child(1) {
    animation-delay: ${variables.delay * 0}s;
  }

  &:nth-child(2) {
    animation-delay: ${variables.delay * 1}s;
  }

  &:nth-child(3) {
    animation-delay: ${variables.delay * 2}s;
  }

  &:nth-child(4) {
    animation-delay: ${variables.delay * 3}s;
  }
`;

// prettier-ignore
const RadarWrapper = styled.div`
  position: relative;
  height: ${variables.size * 2}px;
  width: ${variables.size * 2}px;
  transform: translate(25% , 25%);
`;

function Radar({ ...props }) {
  return (
    <RadarWrapper {...props}>
      <Dot />
      <Dot />
      <Dot />
      <Dot />
    </RadarWrapper>
  );
}

export default Radar;



