import { isValidElement } from 'react';
import UnserializeableInputError from './UnserializeableInputError';
import Callback from './Callback';

// This file acts similarly to devtools' dehydrate.js
//   https://github.com/facebook/react-devtools/blob/master/agent/dehydrate.js

// However we have a different set of requirements in mind -- we just want to
// serialize things that we can sensibly unserialize at the other end.

function getType(data) {
  const type = typeof data;

  if (type === 'object') {
    if (!data) {
      return null;
    }

    if (Array.isArray(data)) {
      return 'array';
    }

    if (isValidElement(data)) {
      return 'element';
    }

    if (data instanceof Date) {
      return 'date';
    }

    if (Object.getPrototypeOf(data) !== Object.prototype) {
      return 'instance';
    }
  }

  return type;
}

// We don't need every property of an element
function normalizeElement({ type, props, state, context }) {
  const name = typeof type === 'string' ? type : type.name;

  // eslint-disable-next-line no-use-before-define
  return normalize({
    name,
    props,
    state,
    context,
  });
}

export default function normalize(data) {
  switch (getType(data)) {
    case 'element': {
      return normalizeElement(data);
    }
    case 'array': {
      return data.map(normalize);
    }
    case 'object': {
      const result = {};
      Object.keys(data).forEach(key => {
        result[key] = normalize(data[key]);
      });
      return result;
    }
    case 'function': {
      return new Callback(data.name);
    }
    case 'instance': {
      throw new UnserializeableInputError(data);
    }
    default: {
      return data;
    }
  }
}



