export function formatSnapshotStatus(status) {
  switch (status) {
    case 'SNAPSHOT_IN_PROGRESS':
      return 'Snapshotting';
    case 'SNAPSHOT_PASSED':
      return 'Unchanged';
    case 'SNAPSHOT_PENDING':
      return 'Unreviewed';
    case 'SNAPSHOT_ACCEPTED':
      return 'Accepted';
    case 'SNAPSHOT_DENIED':
      return 'Denied';
    case 'SNAPSHOT_CAPTURE_ERROR':
      return 'Component error';
    case 'SNAPSHOT_SPEC_DELETED':
      return 'Deleted';
    default:
      throw new Error('unimplemented');
  }
}

export function formatSnapshotBadge(status) {
  switch (status) {
    case 'loading':
      return 'loading';
    case 'SNAPSHOT_PENDING':
      return 'warning';
    case 'SNAPSHOT_ACCEPTED':
      return 'positive';
    case 'SNAPSHOT_DENIED':
      return 'negative';
    case 'SNAPSHOT_CAPTURE_ERROR':
      return 'error';
    case 'SNAPSHOT_SPEC_DELETED':
      return 'neutral';
    default:
      throw new Error('unimplemented');
  }
}

export function formatBuildBadge(status) {
  switch (status) {
    case 'loading':
      return 'loading';
    case 'BUILD_IN_PROGRESS':
      return 'progress';
    case 'BUILD_PENDING':
      return 'warning';
    case 'BUILD_ACCEPTED':
    case 'BUILD_PASSED':
      return 'positive';
    case 'BUILD_FAILED':
    case 'BUILD_DENIED':
      return 'negative';
    case 'BUILD_ERROR':
    case 'BUILD_TIMED_OUT':
      return 'error';
    default:
      throw new Error('unimplemented');
  }
}

export function formatBuildStatus(status) {
  switch (status) {
    case 'loading':
      return 'Loading';
    case 'BUILD_IN_PROGRESS':
      return 'In progress';
    case 'BUILD_PENDING':
      return 'Unreviewed';
    case 'BUILD_ACCEPTED':
    case 'BUILD_PASSED':
      return 'Passed';
    case 'BUILD_DENIED':
    case 'BUILD_FAILED':
      return 'Failed';
    case 'BUILD_ERROR':
      return 'Errored';
    case 'BUILD_TIMED_OUT':
      return 'Timed out';
    default:
      throw new Error('unimplemented');
  }
}



