import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { color, typography } from './../shared/styles';
import { inlineGlow } from './../shared/animation';
import Link from './Link';
import Icon from './Icon';

// prettier-ignore
const Loading = styled.span`
  ${inlineGlow};
  margin-right: .5em;
`;

// prettier-ignore
const Crumb = styled.div`
  font-size: ${typography.size.s2}px;
  font-weight: ${typography.weight.extrabold};
  display: inline-block;

  svg {
    display: inline-block;
    height: .85em;
    width: .85em;
    vertical-align: text-top;
    position: relative;
    bottom: -.225em;
    margin: 0 .25em;

    path {
      fill: ${props => props.inverse ? color.lightest : color.mediumdark };
    }
  }
`;

const BreadcrumbWrapper = styled.div``;

function Breadcrumb({ loading, crumbs, inverse, LinkWrapper, ...props }) {
  const crumbsLinks = crumbs.map(({ pathname, href }, index) => {
    let link = (
      <Link inverse={inverse} tertiary={!inverse} href={href}>
        {pathname}
      </Link>
    );
    if (LinkWrapper) {
      link = <LinkWrapper href={href}>{link}</LinkWrapper>;
    }

    return (
      <Crumb key={pathname} inverse={inverse} loading={loading}>
        {!loading && link}
        {loading && <Loading>{pathname}</Loading>}
        {!loading && index !== crumbs.length - 1 && <Icon icon="arrowright" inverse={inverse} />}
      </Crumb>
    );
  });

  return (
    <BreadcrumbWrapper loading={loading} {...props}>
      {crumbsLinks}
    </BreadcrumbWrapper>
  );
}

Breadcrumb.propTypes = {
  loading: PropTypes.bool,
  crumbs: PropTypes.arrayOf(
    PropTypes.shape({
      pathname: PropTypes.string.isRequired,
      href: PropTypes.string.isRequired,
    })
  ),
  inverse: PropTypes.bool,
  LinkWrapper: PropTypes.func,
};

Breadcrumb.defaultProps = {
  loading: false,
  crumbs: [
    {
      pathname: 'Loading',
      href: '/loading',
    },
    {
      pathname: 'Just-one',
      href: '/loading',
    },
    {
      pathname: 'Moment',
      href: '/loading',
    },
  ],
  inverse: false,
  LinkWrapper: null,
};

export default Breadcrumb;



