/* eslint-env browser */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { observer } from 'mobx-react';

import { Link, Input, Clipboard, styles, animation } from '../../../../../lib/components';
import WidgetButton from '../components/WidgetButton';
import WidgetButtonTest from '../components/WidgetButtonTest';
import WidgetNotification from '../components/WidgetNotification';
import WidgetShade from '../components/WidgetShade';
import WidgetScanner from '../components/WidgetScanner';

const { spacing } = styles;
const { jiggle } = animation;

const Launcher = styled(WidgetButton)``;

// prettier-ignore
const Tester = styled(WidgetButtonTest)`
  margin-left: 10px;
  ${props => props.highlight && css`
    animation: ${jiggle} 1s ease-out 1s 1;
  `}
`;

const Buttons = styled.div`
  position: fixed;
  left: ${spacing.padding.medium}px;
  bottom: ${spacing.padding.medium}px;
  z-index: 444444;
  white-space: nowrap;

  ${Launcher}, ${Tester} {
    display: inline-block;
  }
`;

const Shade = styled(WidgetShade)`
  position: fixed;
  z-index: 111111;
  bottom: 0;
  left: 0;
  pointer-events: none;
  transition: opacity 0.25s ease-out;
  opacity: ${props => (props.active ? 1 : 0)};
`;

const Notification = styled(WidgetNotification)``;

const Notifications = styled.div`
  position: fixed;
  left: ${spacing.padding.medium}px;
  bottom: 50px;
  z-index: 333333;

  ${Notification} + ${Notification} {
    margin-top: 10px;
  }
`;

const CodeCopyClipboard = styled(Clipboard)`
  width: 100%;
  margin: 0.5rem 0 0;
`;

const Chromatic = styled.div`
  position: fixed;
  width: 0px;
  height: 0px;
  bottom: 0px;
  right: 0px;
  z-index: 2147483647;
`;

// The indexes we move to when we see these things, if we are on-boarding:
const FIRST_COMPONENT_INDEX = 3;

function shouldOnboard({ loading, componentCount, detectedSpecs, criticalError }) {
  return !loading && componentCount === 0 && detectedSpecs.length > 0 && !criticalError;
}

class WidgetScreen extends Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      // You only enter onboarding if you start with no components
      appOnboardingIndex: shouldOnboard(props) ? 0 : -1,
      showBuildInstructions: false,
    };

    this.onDeactivate = this.onDeactivate.bind(this);
    this.onActivate = this.onActivate.bind(this);
    this.onToggleActive = this.onToggleActive.bind(this);
    this.onDocumentKeyDown = this.onDocumentKeyDown.bind(this);
    this.onNextOnboardingStep = this.onNextOnboardingStep.bind(this);
    this.onShowBuildInstructions = this.onShowBuildInstructions.bind(this);
  }

  componentWillMount() {
    document.addEventListener('keydown', this.onDocumentKeyDown, false);
  }

  componentWillReceiveProps(newProps) {
    this.setState(({ appOnboardingIndex }) => {
      if (appOnboardingIndex === -1) {
        if (shouldOnboard(newProps)) {
          return { appOnboardingIndex: 0 };
        }
        return {};
      }

      if (newProps.componentCount > 0) {
        return {
          appOnboardingIndex: Math.max(appOnboardingIndex, FIRST_COMPONENT_INDEX),
        };
      }
      return {};
    });
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.onDocumentKeyDown);
  }

  onDeactivate() {
    this.props.onActivate(false);
  }

  onActivate() {
    this.props.onActivate(true);
    this.setState(({ appOnboardingIndex }) => ({
      appOnboardingIndex: appOnboardingIndex === 0 ? 1 : appOnboardingIndex,
    }));
  }

  onToggleActive() {
    if (this.props.active) {
      this.onDeactivate();
    } else {
      this.onActivate();
    }
  }

  onDocumentKeyDown(event) {
    if (event.ctrlKey && event.key === 'm') {
      this.onToggleActive();
    }
  }

  onNextOnboardingStep() {
    const { appOnboardingIndex } = this.state;
    if (appOnboardingIndex === -1) {
      return;
    }
    this.setState({
      appOnboardingIndex: appOnboardingIndex < 4 ? appOnboardingIndex + 1 : -1,
    });
  }

  onShowBuildInstructions() {
    this.setState({ showBuildInstructions: true });
    this.onNextOnboardingStep();
  }

  render() {
    const {
      loading,
      componentCount,
      detectedReact,
      detectedSpecs,
      active,
      testCommand,
      activeError,
      criticalError,
      onCreateSpec,
      onShowComponentSpecs,
    } = this.props;
    const { appOnboardingIndex, showBuildInstructions } = this.state;

    const componentNames = {};
    detectedSpecs.forEach(s => {
      if (s.status !== 'unsaveable') {
        componentNames[s.component.name] = true;
      }
    });
    const detectedComponentCount = Object.keys(componentNames).length;

    const notifications = [];

    if (!loading && !detectedReact) {
      notifications.push(
        <Notification
          key="no-react"
          title="We didn&rsquo;t detect React on this page."
          desc={
            <span>
              You need to ensure that you import <code>react-chromatic</code>{' '}
              <strong>before</strong> <code>react</code>.
              <Link href="http://docs.chromaticqa.com/#client">Read more here</Link>.
            </span>
          }
        />
      );
    } else if (!loading && detectedSpecs.length === 0) {
      notifications.push(
        <Notification
          key="no-components"
          title="We didn&rsquo;t find saveable components on this page."
          desc={
            <span>
              Browse around your app to find components to save. Some types of components{' '}
              <Link href="http://docs.chromaticqa.com/components">aren&rsquo;t supported yet</Link>.
            </span>
          }
        />
      );
    }

    if (appOnboardingIndex === 0) {
      notifications.push(
        <Notification
          key="welcome"
          title={'👋\u00A0 Chromatic is super easy to setup.'}
          desc="We’ll show you how to start getting UI tests in less than 3 minutes."
          links={[{ title: 'Get started', onClick: this.onActivate }]}
        />
      );
    }
    if (appOnboardingIndex === 1) {
      const nDisplayed = Math.min(3, detectedComponentCount);
      const displayedNames = Object.keys(componentNames).slice(0, nDisplayed);
      let componentNameStr = displayedNames.slice(0, -1).join(', ');
      if (nDisplayed > 1) {
        componentNameStr += ` and ${displayedNames.slice(-1)[0]}`;
      }
      notifications.push(
        <Notification
          key="autoFind"
          title="Chromatic automatically finds components on screen."
          desc={`We found ${detectedComponentCount} components including ${componentNameStr}.`}
          progress={[1, 3]}
          links={[{ title: 'Continue', onClick: this.onNextOnboardingStep }]}
        />
      );
    }
    if (appOnboardingIndex === 2) {
      notifications.push(
        <Notification
          key="addSpec"
          title="Add test coverage by saving component specs."
          desc="Saved specs are snapshotted for visual UI testing."
          progress={[2, 3]}
          figure="https://www.chromaticqa.com/static/widget-save-animation.gif"
        />
      );
    }
    if (appOnboardingIndex === 3) {
      const addedSpec = detectedSpecs.find(s => s.status === 'saved' || s.status === 'saving');

      // If the user browses away from the page they were looking at,
      // The spec may disappear. That's OK, we will just stop onboarding
      if (addedSpec) {
        const addedComponentName = addedSpec.component.name;
        notifications.push(
          <Notification
            key="runTest"
            title={`🎉\u00A0 You added test coverage for ${addedComponentName}!`}
            desc={
              <span>
                Future builds will now be tested using this spec. Now try starting a test. Just run
                the following command:
                <CodeCopyClipboard toCopy={testCommand}>
                  <Input value={testCommand} appearance="code" readOnly />
                </CodeCopyClipboard>
              </span>
            }
            progress={[3, 3]}
          />
        );
      }
    }
    if (showBuildInstructions) {
      notifications.push(
        <Notification
          key="start-build"
          title="Run a Chromatic test"
          desc={
            <span>
              with the following command.{' '}
              <Link secondary href="http://docs.chromaticqa.com/#setup_ci">
                <b>Learn more</b>
              </Link>
              <br />
              <CodeCopyClipboard toCopy={testCommand}>
                <Input value={testCommand} appearance="code" readOnly />
              </CodeCopyClipboard>
            </span>
          }
        />
      );
    }
    if (criticalError === 'no-app-code') {
      notifications.push(
        <Notification
          key="no-app-code"
          title="Forgot your app code?"
          desc={
            <span>
              Find your <code>appCode</code> at ChromaticQA.com then use it when initializing the
              client.
            </span>
          }
          links={[
            {
              title: 'View guide',
              href: 'http://docs.chromaticqa.com/',
              target: '_blank',
            },
            {
              title: 'Go to Chromatic',
              href: 'http://chromaticqa.com',
              target: '_blank',
            },
          ]}
        />
      );
    }
    if (criticalError === 'bad-app-code') {
      notifications.push(
        <Notification
          key="bad-app-code"
          title="Wrong app code?"
          desc={
            <span>
              Make sure you&rsquo;re using the correct <code>appCode</code> when initializing the
              client.
            </span>
          }
          links={[
            {
              title: 'View guide',
              href: 'http://docs.chromaticqa.com/',
              target: '_blank',
            },
          ]}
        />
      );
    }
    if (activeError === 'no-isolator-connection') {
      notifications.push(
        <Notification
          key="no-isolator-connection"
          title="Failed to connect to Chromatic Server."
          desc="Ensure you’re running it in your node process and have set ports correctly."
          links={[
            {
              title: 'Learn More',
              href: 'http://docs.chromaticqa.com/',
              target: '_blank',
            },
          ]}
        />
      );
    }

    return (
      <div>
        <Chromatic loading={loading} active={active}>
          <Buttons>
            <Launcher
              loading={loading}
              error={!!criticalError}
              active={active}
              count={detectedComponentCount}
              onClick={this.onToggleActive}
            />
            {componentCount > 0 && (
              <Tester onClick={this.onShowBuildInstructions} highlight={appOnboardingIndex === 3} />
            )}
          </Buttons>

          <Notifications>{notifications}</Notifications>
          <Shade active={active} />
        </Chromatic>

        {active && (
          <WidgetScanner
            specs={detectedSpecs}
            onShowComponentSpecs={onShowComponentSpecs}
            onCreateSpec={onCreateSpec}
          />
        )}
      </div>
    );
  }
}

WidgetScreen.propTypes = {
  loading: PropTypes.bool,
  componentCount: PropTypes.number,
  detectedReact: PropTypes.bool.isRequired,
  detectedSpecs: PropTypes.arrayOf(
    PropTypes.shape({
      internalId: PropTypes.string.isRequired,
      name: PropTypes.string,
      component: PropTypes.shape({
        name: PropTypes.string.isRequired,
      }).isRequired,
      status: PropTypes.oneOf(['unsaved', 'saving', 'saved', 'unsaveable']).isRequired,
      unsaveableReason: PropTypes.string,
      domElement: PropTypes.instanceOf(typeof Element !== 'undefined' ? Element : Object)
        .isRequired,
    }).isRequired
  ).isRequired,
  active: PropTypes.bool.isRequired,
  testCommand: PropTypes.string,
  activeError: PropTypes.oneOf(['no-isolator-connection', null]),
  criticalError: PropTypes.oneOf(['no-app-code', 'bad-app-code', null]),
  onCreateSpec: PropTypes.func,
  onShowComponentSpecs: PropTypes.func.isRequired,
  onActivate: PropTypes.func.isRequired,
};

WidgetScreen.defaultProps = {
  loading: false,
  componentCount: 0,
  detectedSpecs: [],
  testCommand: 'loading',
  activeError: null,
  criticalError: null,
  appOnboarded: false,
  appOnboardingIndex: 0,
  appHasComponents: true,
  onCreateSpec: () => 0,
};

export default observer(WidgetScreen);



