/* eslint-env browser */
/* eslint-disable no-console,import/first */
import { detectedReact } from './lib/devtools';

import React from 'react';
import { render } from 'react-dom';
import { reaction } from 'mobx';

import './lib/polyfill';
import detectSpecs from './lib/specs';
import App from './App';

import startIsolator from './lib/isolator';
import { requireContextToComponentMap, arrayToComponentMap } from './lib/components';

const { CHROMATIC_INDEX_URL = 'https://www.chromaticqa.com' } = process.env;

function drawWidget({ indexUrl, appCode, isolatorOptions, specs }) {
  const div = document.createElement('div');
  div.id = 'chromatic-root';
  document.body.appendChild(div);

  reaction(
    () => ({
      hasDetectedReact: detectedReact.get(),
      simpleSpecs: specs.slice(),
    }),
    ({ hasDetectedReact, simpleSpecs }) => {
      render(
        <App
          indexUrl={indexUrl}
          appCode={appCode}
          isolatorOptions={isolatorOptions}
          detectedReact={hasDetectedReact}
          specs={simpleSpecs}
        />,
        div
      );
    },
    { delay: 200 }
  );
}

let options;
function configure(userOptions) {
  if (options) {
    throw new Error('Chromatic already configured');
  }
  options = userOptions;

  const {
    appCode,
    indexUrl = CHROMATIC_INDEX_URL,
    components,
    componentContext,
    noWidget = false,
    runtimes = [],
    noTunnel = false,
    port = 3004,
    log = 'debug',
  } = options;

  // TODO -- deal with other formats of component input
  let componentMap;
  if (componentContext) {
    componentMap = requireContextToComponentMap(componentContext);
  } else if (components) {
    componentMap = arrayToComponentMap(components);
  } else if (!noWidget) {
    throw new Error('You need to provide either a `componentContext` or a list of `components`');
  }

  // If we are rendered in an iframe, (by ourself), then we need to clear
  // the screen right away, rather than waiting for a spec
  const isIsolator = window.frameElement && window.frameElement.dataset.chromaticIsolator;
  startIsolator({
    components: componentMap,
    runtimes,
    clearScreen: isIsolator,
  });

  if (!isIsolator && !noWidget) {
    const componentNames = Object.keys(componentMap);
    console.log(`Found components: ${componentNames.join(', ')}`);

    const specs = detectSpecs({
      componentNames,
      log,
    });

    drawWidget({
      appCode,
      indexUrl,
      isolatorOptions: { noTunnel, port },
      specs,
    });
  }
}

export default configure;



