import { graphql } from 'react-apollo';
import gql from 'graphql-tag';
import { action } from 'mobx';
import { stringify } from '../../../../../lib/specs/serialize';

export default graphql(
  gql`
    mutation WidgetCreateSpec($input: CreateSpecInput!) {
      createSpec(input: $input) {
        id
        number
        component {
          specCount
        }
      }
    }
  `,
  {
    props: ({ mutate, ownProps: { data: { app } } }) => ({
      onCreateSpec: action(async spec => {
        // eslint-disable-next-line no-param-reassign
        spec.status = 'saving';

        const { component: { name: componentName }, name, input } = spec;
        const {
          data: { createSpec: { id, number, component: { specCount } } },
          error,
        } = await mutate({
          variables: {
            input: {
              appId: app.id,
              componentName,
              name,
              input: stringify(input),
            },
          },
        });
        // FIXME
        if (error) {
          throw error;
        }

        // eslint-disable-next-line no-param-reassign
        spec.id = id;
        // eslint-disable-next-line no-param-reassign
        spec.status = 'saved';
        // eslint-disable-next-line no-param-reassign
        spec.number = number;
        // eslint-disable-next-line no-param-reassign
        spec.component = {
          ...spec.component,
          specCount,
        };
      }),
    }),
  }
);



