import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { FadeInUp } from 'animate-css-styled-components';
import { observer } from 'mobx-react';

import { Button, Icon, styles, animation } from '../../../../../lib/components';

const { color, typography } = styles;
const { rotate360 } = animation;

// prettier-ignore
const Count = styled.span`
  font-weight: ${typography.weight.regular};
  margin-left: 6px;
`;

// prettier-ignore
const ScannerButton = styled(Button)`
  font-size: 11px;
  line-height: 12px;
  padding: 5px 12px 3px;
  margin-right: 6px;
  vertical-align: top;
`;

const ComponentButton = ScannerButton.extend`
  background: rgba(31, 31, 31, 0.6);
  color: ${color.lightest};
`;

// prettier-ignore
const SaveButton = ScannerButton.extend`
  svg {
    ${props => props.saving && css`
        animation: ${rotate360} 1s linear infinite;
    `} height: 12px;
    width: 12px;
    margin-right: 2px;
    margin-top: -1px;
  }

  ${props => props.error && css`
    opacity: 1;
    font-weight: ${typography.weight.regular};
    color: ${color.negative};
    svg path { fill: ${color.negative}; }
  `}
`;

const SaveHref = SaveButton.extend``;

// prettier-ignore
const ButtonGroup = styled.div`
  white-space: nowrap;
  margin: 8px 0;
  font-size: 0;
`;

function ScannerButtons({
  spec: { status, internalId, component: { name, specCount }, unsaveableReason },
  onCreateSpec,
  onShowComponentSpecs,
  ...props
}) {
  let count = `${specCount} ${specCount === 1 ? 'spec' : 'specs'}`;
  if (specCount === 0) {
    count = 'No specs';
  }

  let saveButton = (
    <SaveButton secondary onClick={onCreateSpec}>
      Save
    </SaveButton>
  );
  if (status === 'saved') {
    saveButton = (
      <SaveButton tertiary disabled>
        Saved
      </SaveButton>
    );
  } else if (status === 'saving') {
    saveButton = (
      <SaveButton tertiary disabled saving>
        <Icon icon="sync" /> Saving
      </SaveButton>
    );
  } else if (status === 'unsaveable') {
    // TODO -- link to docs on click?
    // github.com/hichroma/chromatic/issues/156
    saveButton = (
      <SaveHref tertiary error href="http://docs.chromaticqa.com/specs#cant-save" target="_blank">
        <Icon icon="info" /> {unsaveableReason || "Can't save"}
      </SaveHref>
    );
  }

  let onComponentButtonClick = () => {};
  if (status !== 'unsaveable') {
    onComponentButtonClick = () => onShowComponentSpecs(name, internalId);
  }

  return (
    <FadeInUp duration="0.25s">
      <ButtonGroup {...props}>
        <ComponentButton onClick={onComponentButtonClick}>
          {name}
          <Count>{count}</Count>
        </ComponentButton>
        {saveButton}
      </ButtonGroup>
    </FadeInUp>
  );
}

ScannerButtons.propTypes = {
  spec: PropTypes.shape({
    status: PropTypes.oneOf(['unsaved', 'saving', 'saved', 'unsaveable']).isRequired,
    unsaveableReason: PropTypes.string,
    component: PropTypes.shape({
      name: PropTypes.string.isRequired,
      specCount: PropTypes.number.isRequired,
    }).isRequired,
  }).isRequired,
  onCreateSpec: PropTypes.func.isRequired,
  onShowComponentSpecs: PropTypes.func.isRequired,
};

export default observer(ScannerButtons);



