import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { getContext } from 'recompose';

import { background, spacing } from './../shared/styles';

// prettier-ignore
const Drawer = styled.tr`
  td {
    background-color: ${background.app};
    box-shadow: rgba(0,0,0,0.05) 0 5px 5px 0 inset, rgba(0,0,0,0.05) 0 -3px 5px 0 inset;
    padding: 0 !important;
  }
`;

// prettier-ignore
const DrawerContents = styled.div`
  padding: ${spacing.padding.large}px;
`;

function TableDrawer({ drawer, openDrawers, children, ...other }) {
  if (!openDrawers[drawer]) {
    return <tr />;
  }
  return (
    <Drawer>
      <td {...other}>
        <DrawerContents>{children}</DrawerContents>
      </td>
    </Drawer>
  );
}

TableDrawer.propTypes = {
  drawer: PropTypes.number.isRequired,
  // Not sure how to express this prop type in a way React likes
  // eslint-disable-next-line react/forbid-prop-types
  openDrawers: PropTypes.any.isRequired,
  children: PropTypes.node.isRequired,
};

export default getContext({ openDrawers: PropTypes.any })(TableDrawer);



