import React from 'react';
import styled, { css } from 'styled-components';
import { background, color, typography } from './../shared/styles';

// prettier-ignore
const BadgeWrapper = styled.div`
  display: inline-block;
  vertical-align: top;
  font-size: ${props => props.appearance === "change" ? typography.size.s2 : '11'}px;
  line-height: ${props => props.appearance === "change" ? '20' : '12'}px;
  padding: ${props => props.appearance === "change" ? '4px 6px' : '5px 12px 3px'};
  border-radius: ${props => props.appearance === "change" ? '4px' : '3em'};

  ${props => props.status === 'positive' && css`
    color: ${color.positive};
    background: ${background.positive};
	`}
  ${props => props.status === 'negative' && css`
    color: ${color.negative};
    background: ${background.negative};
	`}
  ${props => props.status === 'warning' && css`
    color: ${color.warning};
    background: ${background.warning};
	`}

  ${props => props.status === 'error' && css`
    color: ${color.lightest};
    background: ${color.negative};
    font-weight: ${typography.weight.bold};
	`}

  ${props => props.status === 'neutral' && css`
    color: ${color.dark};
    background: ${color.mediumlight};
	`}

  ${props => props.appearance === 'change' && css`
    color: ${color.dark};
  `}
`;

function Badge({ ...props }) {
  return <BadgeWrapper {...props} />;
}

export default Badge;



