import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { color } from './../shared/styles';
import { inlineGlow } from './../shared/animation';

// prettier-ignore
const Left = styled.div``;
const Center = styled.div``;
const Right = styled.div``;

// prettier-ignore
const SubheaderWrapper = styled.div`
  color: ${color.dark};
  display: flex;
  margin-bottom: .75rem;
  line-height: 28px;

  ${Center} { flex: 0 1 auto; }

  ${Left},
  ${Right} { flex: 1; }

  ${Right} { text-align: right; }

  ${props => props.loading && css`
    ${Left} * { ${inlineGlow} }
  `}
`;

export default function Subheader({ loading, left, center, right, ...props }) {
  return (
    <SubheaderWrapper {...props} loading={loading}>
      {left && <Left>{left}</Left>}
      {!loading && center && <Center>{center}</Center>}
      {!loading && right && <Right>{right}</Right>}
    </SubheaderWrapper>
  );
}

Subheader.propTypes = {
  loading: PropTypes.bool,
  left: PropTypes.node,
  center: PropTypes.node,
  right: PropTypes.node,
};
Subheader.defaultProps = {
  loading: false,
  left: <span>Loading</span>,
  center: null,
  right: null,
};



