import { extendObservable } from 'mobx';

import { parse, stringify } from '../../../../../lib/specs/serialize';

// TODO: Make this a hash? - https://github.com/hichroma/chromatic/issues/157
export function hashSpecInput(input) {
  const { element, ancestors } = typeof input === 'string' ? parse(input) : input;
  return stringify({ element, ancestors });
}

export default class Spec {
  constructor({ id, name, component, number, input, key, domElement, node }) {
    this.input = typeof input === 'string' ? parse(input) : input;
    this.node = node;

    this.key = key || hashSpecInput(this.input);
    this.internalId = this.id || this.key;
    this.domElement = domElement;

    extendObservable(this, {
      id,
      name,
      status: id ? 'saved' : 'unsaved',
      component: component || { name: this.input.element.name },
      number,
    });
  }
}



