import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { hoistStatics, withState } from 'recompose';

import { background, color, typography, paper, breakpoint } from './../shared/styles';
import { inlineGlow } from './../shared/animation';
import Icon from './../basics/Icon';
import Spinner from './../interstitial/Spinner';
import Link from './../basics/Link';
import IsolatorFrame from './IsolatorFrame';

const LabelLink = styled(Link)`
  margin-left: 6px;

  color: ${color.mediumdark};
  &:hover {
    color: ${color.darkest};
  }
  &:active {
    color: ${color.mediumdark};
  }
`;

// prettier-ignore
const LabelText = styled.span`
  font-weight: ${typography.weight.bold};
  line-height: 16px;
`;

// prettier-ignore
const Label = styled.div`
  font-size: ${typography.size.s2}px;
  padding: 12px 20px;
  display: block;
  border-bottom: 1px solid ${color.mediumlight};
`;

const ErrorMessage = styled.div`
  position: absolute;
  bottom: 16px;
  right: 20px;
  z-index: 2;
  padding: 3px 5px;

  background: ${background.warning};
  color: ${color.dark};
  border-radius: 4px;
  font-size: ${typography.size.s1}px;
  line-height: 18px;

  svg {
    path {
      fill: ${color.dark};
    }
    height: 12px;
    width: 12px;
    margin-right: 6px;
  }
`;

const Action = styled(Link)``;

// prettier-ignore
const ActionGroup = styled.div`
  font-size: ${typography.size.s1}px;
  position: absolute;
  bottom: 20px;
  left: 20px;
  z-index: 1;

  ${Action} + ${Action} {margin-left: 10px;}
`;

const LoadingSpinner = styled(Spinner)`
  flex: 0 0 auto;

  ${'' /* //overrides */} position: relative;
  margin: 0;
  top: auto;
  left: auto;
`;

const CanvasScroll = styled.div`
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  position: relative;
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;

  @media (min-width: ${breakpoint}px) {
    padding: 1rem;
  }
`;

// prettier-ignore
const CanvasWrapper = styled.div`
  ${paper};
  min-height: 10em;
  position: relative;
  display: flex;
  flex-direction: column;


  ${props => props.loading && css`
    ${Label},
    ${ActionGroup} ${Action} {
      ${inlineGlow};

      svg { display: none; }
    }
	`}
`;

// prettier-ignore
const Iframe = styled(IsolatorFrame)`
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  margin: 0;
  display: block;
`;

// prettier-ignore
const Img = styled.img`
  display: block;
  max-width: 100%;
  height: auto;
`;

function Canvas({
  loading,
  label,
  labelLinkText,
  labelLink,
  LinkWrapper,
  interactive,
  figure,
  spec,
  error,
  setError,
  ...props
}) {
  const onConnectionError = (on = true) => {
    setError(
      on ? (
        <span>
          Failed to connect to Chromatic Isolator iframe. <br />
          <Link href="http://docs.chromaticqa.com/configuration" target="_blank">
            Read more
          </Link>
        </span>
      ) : (
        false
      )
    );
  };
  const onRenderingError = message => {
    setError(
      <span>
        Failed to render spec: <br />
        <code style={{ fontSize: '11px' }}>{message}</code>
        <br />
        <Link href="http://docs.chromaticqa.com/specs" target="_blank">
          Read more
        </Link>
      </span>
    );
  };

  let visualization = <LoadingSpinner />;
  if (!loading) {
    if (interactive) {
      visualization = (
        <Iframe
          title={label}
          src={figure}
          width="400"
          height="300"
          spec={spec}
          onConnectionError={onConnectionError}
          onRenderingError={onRenderingError}
        >
          <p>Your browser does not support iframes.</p>
        </Iframe>
      );
    } else {
      visualization = <Img alt={label} src={figure} />;
    }
  }

  let canvasLink = (
    <LabelLink tertiary href={labelLink}>
      {labelLinkText}
    </LabelLink>
  );

  if (LinkWrapper) {
    canvasLink = <LinkWrapper href={labelLink}>{canvasLink}</LinkWrapper>;
  }

  return (
    <CanvasWrapper loading={loading} {...props}>
      {!loading &&
      label && (
        <Label>
          <LabelText>{label}</LabelText>
          {labelLink && canvasLink}
        </Label>
      )}

      {error && (
        <ErrorMessage>
          <Icon icon="alert" />
          {error}
        </ErrorMessage>
      )}
      <ActionGroup>
        {/* { (
          <Action secondary>
            <Icon icon="grow" />1080x840
          </Action>
        )} */}
      </ActionGroup>
      <CanvasScroll withLabel={label}>{figure && visualization}</CanvasScroll>
    </CanvasWrapper>
  );
}

Canvas.propTypes = {
  loading: PropTypes.bool,
  label: PropTypes.string,
  labelLinkText: PropTypes.string,
  labelLink: PropTypes.string,
  LinkWrapper: PropTypes.func,
  interactive: PropTypes.bool,
  figure: PropTypes.string.isRequired,
  spec: PropTypes.shape({
    input: PropTypes.any.isRequired,
  }),
  error: PropTypes.node.isRequired,
  setError: PropTypes.func.isRequired,
};

Canvas.defaultProps = {
  loading: false,
  label: null,
  labelLinkText: null,
  labelLink: null,
  LinkWrapper: null,
  interactive: false,
  figure: 'http://via.placeholder.com/350x150',
  spec: { input: {} },
};

export default hoistStatics(withState('error', 'setError', false))(Canvas);



