/* eslint-env browser */
import React from 'react';
import PropTypes from 'prop-types';
import { graphql } from 'react-apollo';
import gql from 'graphql-tag';
import { compose, withState } from 'recompose';

import WidgetScreen from '../screens/WidgetScreen';
import withOnCreateSpec from '../lib/withOnCreateSpec';
import Spec, { hashSpecInput } from '../data/Spec';
import ErrorSpec from '../data/ErrorSpec';

const withActiveError = withState('activeError', 'onSetActiveError', null);

const withSavedSpecs = graphql(
  gql`
    query AgentWidgetPageQuery {
      app {
        id
        code
        components {
          id
          name
          specCount(runtime: "")
          specs(runtime: "") {
            id
            input
          }
        }
      }
    }
  `,
  {
    options: () => ({
      pollInterval: 2000,
    }),
  }
);

function WidgetPage({
  data: { loading, app },
  detectedReact,
  detectedSpecs,
  active,
  activeError,
  criticalError,
  onCreateSpec,
  onShowComponentSpecs,
  onActivate,
}) {
  if (loading) {
    return (
      <WidgetScreen
        loading
        detectedReact={detectedReact}
        detectedSpecs={detectedSpecs}
        active={active}
        onShowComponentSpecs={onShowComponentSpecs}
        onActivate={onActivate}
      />
    );
  }
  const { components } = app;

  const savedSpecsByKey = {};
  components.forEach(component => {
    component.specs.forEach(spec => {
      savedSpecsByKey[hashSpecInput(spec.input)] = spec;
    });
  });

  // We want to coalsce saved specs into our detectedSpecs
  // -- ie. figure out which of our detectedSpecs have been saved
  const detectedAndSavedSpecs = detectedSpecs.map(spec => {
    // Ensure we have the component count correct
    const component = components.find(c => c.name === spec.component.name) || {
      ...spec.component,
      specCount: 0,
      specs: [],
    };

    if (spec.error) {
      return new ErrorSpec({
        ...spec,
        component,
      });
    }

    // is there a saved spec with this same key?
    const savedSpec = savedSpecsByKey[spec.key];

    return new Spec({
      ...spec,
      ...savedSpec,
      component,
    });
  });

  const { port } = document.location;
  const { code } = app;
  const testCommand = `./node_modules/.bin/chromatic test -- --app-code=${code} --script-name=none --port ${port}`;

  return (
    <WidgetScreen
      appId={app.id}
      componentCount={components.length}
      detectedReact={detectedReact}
      detectedSpecs={detectedAndSavedSpecs}
      active={active}
      testCommand={testCommand}
      activeError={activeError}
      criticalError={criticalError}
      onCreateSpec={onCreateSpec}
      onShowComponentSpecs={onShowComponentSpecs}
      onActivate={onActivate}
    />
  );
}

WidgetPage.propTypes = {
  data: PropTypes.shape({
    loading: PropTypes.bool,
    app: PropTypes.shape({
      components: PropTypes.arrayOf(
        PropTypes.shape({
          name: PropTypes.string.isRequired,
        })
      ).isRequired,
    }),
    build: PropTypes.shape({
      id: PropTypes.string.isRequired,
      status: PropTypes.string.isRequired,
    }),
  }).isRequired,
  active: PropTypes.bool.isRequired,
  detectedReact: PropTypes.bool.isRequired,
  detectedSpecs: PropTypes.arrayOf(
    PropTypes.shape({
      component: PropTypes.shape({
        name: PropTypes.string.isRequired,
      }).isRequired,
    })
  ).isRequired,
  onCreateSpec: PropTypes.func.isRequired,
  onShowComponentSpecs: PropTypes.func.isRequired,
  onActivate: PropTypes.func.isRequired,
  activeError: PropTypes.oneOf(['no-isolator-connection', null]),
  criticalError: PropTypes.oneOf(['no-app-code', 'bad-app-code', null]),
};

WidgetPage.defaultProps = {
  activeError: null,
  criticalError: null,
};

export default compose(withActiveError, withSavedSpecs, withOnCreateSpec)(WidgetPage);



