import React from 'react';
import PropTypes from 'prop-types';
import { graphql } from 'react-apollo';
import gql from 'graphql-tag';
import { compose } from 'recompose';
import { stringify } from '../../../../../lib/specs/serialize';

import Spec from '../data/Spec';
import SpecsScreen from '../screens/SpecsScreen';
import withOnCreateSpec from '../lib/withOnCreateSpec';

const withSavedSpecs = graphql(
  gql`
    query AgentSpecsPageQuery($componentName: String!) {
      app {
        id
        component(name: $componentName) {
          id
          webUrl
          name
          specs(runtime: "") {
            id
            name
            number
            input
            runtime
          }
        }
      }
    }
  `,
  {
    options: ({ componentName }) => ({
      variables: {
        componentName,
      },
    }),
  }
);

const withOnUpdateOrCreateSpec = graphql(
  gql`
    mutation WidgetUpdateSpec($id: ObjID!, $input: UpdateSpecInput!) {
      updateSpec(id: $id, input: $input) {
        id
        name
        input
      }
    }
  `,
  {
    props: ({ mutate, ownProps: { onCreateSpec } }) => ({
      onUpdateOrCreateSpec: async spec => {
        if (spec.status === 'unsaved') {
          onCreateSpec(spec);
          return;
        }

        // eslint-disable-next-line no-param-reassign
        spec.status = 'saving';

        const { id, name, input } = spec;
        const { error } = await mutate({
          variables: {
            id,
            input: {
              name,
              input: stringify(input),
            },
          },
        });
        // FIXME https://github.com/hichroma/chromatic/issues/116
        if (error) {
          throw error;
        }

        // eslint-disable-next-line no-param-reassign
        spec.status = 'saved';
      },
    }),
  }
);

const withOnRemoveSpec = graphql(
  gql`
    mutation WidgetRemoveSpec($id: ObjID!) {
      removeSpec(id: $id)
    }
  `,
  {
    props: ({ mutate }) => ({
      onRemoveSpec: async spec => {
        await mutate({ variables: { id: spec.id } });
        // eslint-disable-next-line no-param-reassign
        spec.id = null;
        // eslint-disable-next-line no-param-reassign
        spec.status = 'unsaved';
      },
    }),
  }
);

function SpecsPage({
  data: { loading, app },
  componentName,
  specInternalId,
  detectedSpecs,
  isolatorOptions,
  onUpdateOrCreateSpec,
  onRemoveSpec,
  onHideComponentSpecs,
}) {
  if (loading) {
    return (
      <SpecsScreen
        loading
        componentName={componentName}
        specInternalId={specInternalId}
        isolatorOptions={isolatorOptions}
        onUpdateOrCreateSpec={onUpdateOrCreateSpec}
        onRemoveSpec={onRemoveSpec}
        onHideComponentSpecs={onHideComponentSpecs}
      />
    );
  }

  const { component: savedComponent } = app;

  // We only want to save a single spec per key
  const keyToUniqueSpecs = {};

  let webUrl;
  // We have saved specs for this component
  if (savedComponent) {
    webUrl = savedComponent.webUrl;
    // First map over the saved specs;
    savedComponent.specs.forEach(gqlSpec => {
      if (gqlSpec.runtime) {
        return;
      }
      const spec = new Spec(gqlSpec);
      keyToUniqueSpecs[spec.key] = spec;
    });
  }

  // OK, now for the unsaved specs we need to be a bit careful
  detectedSpecs.forEach(spec => {
    if (spec.component.name === componentName && !keyToUniqueSpecs[spec.key]) {
      if (spec.status !== 'unsaveable') {
        keyToUniqueSpecs[spec.key] = spec;
      }
    }
  });

  const component = {
    name: componentName,
    webUrl,
    specs: Object.values(keyToUniqueSpecs),
  };

  return (
    <SpecsScreen
      component={component}
      specInternalId={specInternalId}
      isolatorOptions={isolatorOptions}
      onUpdateOrCreateSpec={onUpdateOrCreateSpec}
      onRemoveSpec={onRemoveSpec}
      onHideComponentSpecs={onHideComponentSpecs}
    />
  );
}

SpecsPage.propTypes = {
  componentName: PropTypes.string.isRequired,
  specInternalId: PropTypes.string.isRequired,
  detectedSpecs: PropTypes.arrayOf(
    PropTypes.shape({
      key: PropTypes.string.isRequired,
      status: PropTypes.oneOf(['saving', 'unsaved', 'saved', 'unsaveable']).isRequired,
      component: PropTypes.shape({
        name: PropTypes.string.isRequired,
      }).isRequired,
    })
  ).isRequired,
  data: PropTypes.shape({
    loading: PropTypes.bool,
    app: PropTypes.shape({
      component: PropTypes.shape({
        webUrl: PropTypes.string.isRequired,
        specs: PropTypes.arrayOf(
          PropTypes.shape({
            runtime: PropTypes.string,
            input: PropTypes.string.isRequired,
          }).isRequired
        ).isRequired,
      }),
    }),
  }).isRequired,
  isolatorOptions: PropTypes.shape({}).isRequired,
  onUpdateOrCreateSpec: PropTypes.func.isRequired,
  onRemoveSpec: PropTypes.func.isRequired,
  onHideComponentSpecs: PropTypes.func.isRequired,
};

export default compose(
  withSavedSpecs,
  withOnCreateSpec,
  withOnUpdateOrCreateSpec,
  withOnRemoveSpec
)(SpecsPage);



