import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import validatorjs from 'validatorjs';
import { observer } from 'mobx-react';

import FormState from './../forms/FormState';
import Link from './../basics/Link';
import Input from './../forms/Input';
import Textarea from './../forms/Textarea';
import { color, typography } from './../shared/styles';
import renderSpecToString from '../../specs/render/toString';

const Editor = styled.form`
  border-top: 1px solid ${color.medium};
  border-bottom: 1px solid ${color.medium};
  font-size: ${typography.size.s2 - 1}px;
  padding: 10px 5px;
  position: relative;
`;

// prettier-ignore
const SpecInput = styled(Input)``;
const SpecTextarea = styled(Textarea)``;

const Fields = styled.div`
  display: table;
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  margin-bottom: 0.75em;

  ${SpecInput}, ${SpecTextarea} {
    border-bottom: 4px solid transparent;
  }

  ${SpecInput}:first-child {
    border-bottom: 12px solid transparent;
  }
`;

// prettier-ignore
const Action = styled(Link)`
  font-weight: ${typography.weight.bold};
  float: right;
  margin-left: 15px;
`;

const ActionDelete = Action.extend`
  float: left;
  margin-left: 0;
`;

// prettier-ignore
const Actions = styled.div`
  overflow: hidden;
`;

const isModifiableProp = (props, propName) =>
  propName !== 'children' && typeof props[propName] === 'string';

function propToString(props, propName) {
  if (propName === 'children') {
    return []
      .concat(props[propName])
      .map(element => renderSpecToString({ input: { element } }))
      .join('\n');
  }

  const value = props[propName];

  return value && value.toString();
}

class SpecEditorFormData extends FormState {
  constructor({ name, props, hooks }) {
    const fields = {
      name: {
        label: 'name',
        value: name,
        placeholder: 'Name this spec',
        rules: 'required|string',
        bindings: 'Default',
      },
    };
    Object.keys(props).forEach(propName => {
      const modifiable = isModifiableProp(props, propName);
      fields[`props-${propName}`] = {
        label: propName,
        value: propToString(props, propName),
        placeholder: `Value for the ${propName} prop`,
        rules: modifiable && 'required|string',
        bindings: 'Default',
        disabled: !modifiable,
      };
    });

    super({ fields }, { plugins: { dvr: validatorjs }, hooks });
  }
}

class SpecEditor extends Component {
  constructor(props, context) {
    super(props, context);
    const { spec: { name, input: { element: { props: inputProps } } } } = props;

    this.onFormSave = this.onFormSave.bind(this);

    this.formData = new SpecEditorFormData({
      name,
      props: inputProps,
      hooks: { onSuccess: this.onFormSave },
    });
  }

  onFormSave(form) {
    const { name, ...propValues } = form.values();
    const props = {};
    Object.keys(propValues).forEach(propName => {
      // Only save the modifiable props; the spec will pass through the rest
      if (isModifiableProp(this.props.spec.input.element.props, propName)) {
        const realPropName = propName.slice(6);
        props[realPropName] = propValues[propName];
      }
    });
    this.props.onSave({
      name,
      props,
    });
  }

  render() {
    const { spec: { input: { element: { props } } }, onCancel, onDelete, ...other } = this.props;

    const { formData } = this;

    const onClickDelete = event => {
      event.preventDefault();
      onDelete();
    };
    const actions = (
      <Actions>
        <Action primary onClick={formData.onSubmit} isButton>
          Save spec
        </Action>
        <Action tertiary onClick={onCancel} isButton>
          Cancel
        </Action>
        {onDelete && (
          <ActionDelete tertiary onClick={onClickDelete} isButton>
            Delete
          </ActionDelete>
        )}
      </Actions>
    );

    return (
      <Editor {...other}>
        <Fields>
          <SpecInput
            // eslint-disable-next-line react/jsx-no-bind
            {...formData.select('name').bind()}
            appearance="code"
            orientation="horizontal"
          />

          {Object.keys(props)
            .filter(p => p !== 'children')
            .map(propName => (
              <SpecInput
                // eslint-disable-next-line react/jsx-no-bind
                {...formData.select(`props-${propName}`).bind()}
                appearance="code"
                orientation="horizontal"
              />
            ))}

          {props.children && (
            <SpecTextarea
              // eslint-disable-next-line react/jsx-no-bind
              {...formData.select(`props-children`).bind()}
              appearance="code"
              orientation="horizontal"
            />
          )}
        </Fields>
        {actions}
      </Editor>
    );
  }
}

SpecEditor.propTypes = {
  spec: PropTypes.shape({
    input: PropTypes.shape({
      element: PropTypes.shape({
        props: PropTypes.any.isRequired,
      }),
    }).isRequired,
  }).isRequired,
  onCancel: PropTypes.func.isRequired,
  onSave: PropTypes.func.isRequired,
  onDelete: PropTypes.func,
};

SpecEditor.defaultProps = {
  onDelete: () => 0,
};

export default observer(SpecEditor);



