import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

import Button from './Button';

// prettier-ignore
const ButtonToggleInner = styled(Button)`
  &:first-child {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    padding-left: ${props => props.small ? '20' : '24' }px;
  }
  &:last-child {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    padding-right: ${props => props.small ? '20' : '24' }px;
  }
  &:not(:first-child):not(:last-child) {
    border-radius: 0;
  }
  &:not(:first-child) {
    margin-left: -1px;
  }

  &:hover {
    position: relative;
    z-index: 1;
  }
`;

// prettier-ignore
const ButtonToggleWrapper = styled.div`
  display: inline-block;
  vertical-align: top;
  white-space: nowrap;
`;

export default function ButtonToggle({ titles, onSelectIndex, selectedIndex, ...props }) {
  return (
    <ButtonToggleWrapper {...props}>
      {titles.map((title, index) => (
        <ButtonToggleInner
          key={title}
          active={index === selectedIndex}
          onClick={() => onSelectIndex(index)}
          outline
          {...props}
        >
          {title}
        </ButtonToggleInner>
      ))}
    </ButtonToggleWrapper>
  );
}

ButtonToggle.propTypes = {
  titles: PropTypes.arrayOf(PropTypes.string.isRequired).isRequired,
  onSelectIndex: PropTypes.func.isRequired,
  selectedIndex: PropTypes.number.isRequired,
};



