import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { darken } from 'polished';
import {
  Icon,
  Logo,
  Spinner,
  WithTooltip,
  TooltipNote,
  styles,
} from '../../../../../lib/components';

const { color, typography } = styles;

// prettier-ignore
const LogoWrapper = styled.span`
  transition: all 150ms ease-out;
  display: inline-block;
  vertical-align: top;
  padding: 3px 3px;
  border-radius: 3em;

  &:not(:only-child) {
    padding-left: 6px;
    padding-right: 4px;
    border-radius: 3em 0 0 3em;
  }

  svg {
    display: block;
    height: 14px;
    width: 14px;
  }
`;

const Error = styled(Icon)`
  path {
    fill: ${color.lightest};
  }
`;

// prettier-ignore
const Count = styled.span`
  transition: all 150ms ease-out;
  box-sizing: border-box;
  display: inline-block;
  vertical-align: top;
  text-align: center;
  font-weight: ${typography.weight.extrabold};
  font-size: 10px;
  background-color: ${color.lightest};
  color: ${color.darker};
  border-radius: 0 3em 3em 0;
  line-height: 20px;
  min-width: 24px;
  padding-left:4px;
  padding-right: 8px;
  transition: all 300ms ease-out;
`;

// prettier-ignore
const Button = styled.button`
  display: block;
	border: 0;
  border-radius: 3em;
  box-shadow: 0 0 5px 0 rgba(0,0,0,0.30);
  padding: 0;
	position: relative;
  outline: 0;
  cursor: pointer;
  transition: all 150ms ease-out;
  height: 20px;

  &:disabled {
    cursor: not-allowed;
  }

  ${props => !props.error && css`
  `}

  &:focus { outline:0; }

  ${LogoWrapper} {
    background: ${color.mediumdark};

    ${props => props.loading && css`
      background: ${color.lightest};
    `}

    ${props => props.active && css`
      background: ${color.secondary};
    `}

  }

  ${props => !props.loading && !props.error && css`
    &:hover {
      transform: translate3d(0,-1px,0);
      box-shadow: 0 0 7px 0 rgba(0,0,0,0.30);

      ${LogoWrapper} {
        background: ${props.active ? darken(0.05, color.secondary) : color.dark };
      }
      ${Count} {
        color: ${color.darkest};
        box-shadow: #D9FAF7 0 0 3em 0 inset;
      }
    }
  `}

  ${props => props.loading && css`
    cursor: progress;
  `}

  &:active { transform: translate3d(0,0,0); }

`;

function WidgetButton({ loading, error, active, count, ...props }) {
  let tooltip = 'Initializing Chromatic';
  if (!loading) {
    if (active) {
      tooltip = 'Hide outlines Ctrl + M';
    } else {
      tooltip = 'Show outlines Ctrl + M';
    }
  }

  const button = (
    <Button loading={loading} error={error} active={active} disabled={loading || error} {...props}>
      <LogoWrapper>
        {loading && <Spinner inForm />}
        {error ? <Error icon="facesad" /> : <Logo inverse />}
      </LogoWrapper>
      {!loading && count > 0 && <Count title={`${count} components on this page`}>{count}</Count>}
    </Button>
  );

  const buttonWithTooltip = (
    <WithTooltip
      placement="top"
      mode="hover"
      hasChrome={false}
      tooltip={<TooltipNote note={tooltip} />}
    >
      {button}
    </WithTooltip>
  );

  let widgetButton;

  if (error) {
    widgetButton = button;
  } else {
    widgetButton = buttonWithTooltip;
  }

  return widgetButton;
}

WidgetButton.propTypes = {
  loading: PropTypes.bool,
  error: PropTypes.bool,
  active: PropTypes.bool,
  count: PropTypes.number,
};

WidgetButton.defaultProps = {
  loading: false,
  error: false,
  active: false,
  count: null,
};

export default WidgetButton;



