import styled from 'styled-components';

// prettier-ignore
const WidgetShade = styled.div`
  background-image: radial-gradient(ellipse at center, rgba(0,0,0,0.1) 0%,rgba(0,0,0,0) 70%);
  width: 25%;
  height: 0;
  padding-bottom: 25%;
  transform: translate3d(-50%, 50%, 0);

`;

export default WidgetShade;



