import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { color, typography } from './../shared/styles';

// prettier-ignore
const Badge = styled.div`
  display: inline-block;
  font-size: 10px;
  font-weight: ${typography.weight.bold};
  line-height: 12px;
  text-align: center;
  padding: 4px 7px 4px;
  border-radius: 3em;

  color: ${color.lightest};

  ${props => props.positive && css`
    background-color: ${color.positive};
    background-image: linear-gradient(20deg, #74E48B 0%, #61D135 100%);
	`}
  ${props => props.negative && css`
    background-color: ${color.negative};
    background-image: linear-gradient(22deg, #EB7E44 0%, #FF4400 100%), linear-gradient(-245deg, rgba(255,219,0,0.30) 5%, rgba(0,0,0,0.00) 100%);
	`}
  ${props => props.warning && css`
    background-color: ${color.warning};
    background-image: linear-gradient(20deg, #FED230 0%, #FEBB30 100%);
	`}
`;
function BadgeCount({ count, ...props }) {
  return <Badge {...props}>{count}</Badge>;
}

BadgeCount.propTypes = {
  count: PropTypes.number.isRequired,
};

export default BadgeCount;



