import renderSpecToElement from '../../../../../lib/specs/render';

function makeNameToComponent(components) {
  return function nameToComponent(name) {
    const isHtmlTag = name.indexOf('(') === -1 && name[0] === name[0].toLowerCase();

    if (isHtmlTag) {
      return name;
    }

    const component = components[name];
    if (!component) {
      throw new Error(`Didn't find component named "${name}".

  Our components are: ${Object.keys(components).join(', ')}`);
    }
    return component;
  };
}

function mapProps(name, value) {
  if (value && value.typeName && value.typeName() === 'ChromaticCallback') {
    const error = `This callback '${value.name}' was not intended to be called during rendering.

Read more about callback detection in Chromatic here: http://docs.chromaticqa.com/specs#spec-uses-callbacks`;
    // eslint-disable-next-line no-new-func
    return new Function(`throw new Error(\`${error}\`)`);
  }
  return value;
}

export default function renderSpec({ input }, components) {
  return renderSpecToElement(
    { input },
    {
      nameToComponent: makeNameToComponent(components),
      mapProps,
    }
  );
}



